import { IRestriction } from '../Restriction.interface';
export class OffsetRestriction implements IRestriction {
    private values: Array<string>;
    constructor(values = []) {
        this.values = values;
    }
    sqlString(): string {
        return `OFFSET ${this.values[0]} `;
    }

    getValues() {
        return this.values;
    }
}