import { IFunctionSync, FUNC_STATE } from "./IFunctionSync";
import { ISyncAOP } from "./SyncAOP.interface";
import { APIFactory } from "../../api/APIFactory";
import { APIDispatch } from "../../api/APIDispatch";
import { saveYearConfigAPI } from "../../api/register/saveYearConfigAPI";
import { SQLiteResponse } from "../../device/sqlite/SQLiteResponse";


export class YearConfigSync implements IFunctionSync {
    private state: number;
    constructor(private apiFactory: APIFactory, private dispatcher: APIDispatch, private PushAOP: ISyncAOP = null, private PullAOP: ISyncAOP = null) {
        this.state = FUNC_STATE.AVAILABLE;
    }

    getState(): FUNC_STATE {
        return this.state;
    }

    getName(): string {
        return 'YEAR_CONFIG'
    }

    async getPushJson(frontendTime) {
        return [];
    }

    async pullData(resp) {
        if (this.PullAOP) {
            resp = await this.PullAOP.execute(resp);
        }
        if (resp.Configurations.length > 0) {
            let saveYearConfig = this.apiFactory.getAPI("saveYearConfig");
            (<saveYearConfigAPI>saveYearConfig).FirstUseAPP = resp.FirstUseAPP;
            (<saveYearConfigAPI>saveYearConfig).yearConfigs = resp.Configurations;
            let saveResp = await this.dispatcher.dispatch(saveYearConfig).toPromise();
            return saveResp;
        }
        else {
            return new SQLiteResponse({ status: true }, []);
        }
    }

    setState(state: FUNC_STATE) {
        this.state = state;
    }

    async getSequentialIDNeeded(): Promise<number> {
        return -1;
    }

    async setSequentialID(ids: Array<string>) {

    }
}