import { Injectable, Injector, Optional, Inject, ErrorHandler } from '@angular/core';
import { Observable } from 'rxjs';
import { StringUtils } from '../utils'
import { ConfigToken } from '../injectionToken';
import { DeviceFactory } from './DeviceFactory';
import { v4 as uuid } from 'uuid';
import { UnbindDeviceAPI } from '../api/register/UnbindDeviceAPI';
import { APIFactory } from '../api/APIFactory';
import { APIDispatch } from '../api/APIDispatch';

declare var device;
declare var navigator;
declare var cordova;
declare var IRoot;

@Injectable({
  providedIn: 'root'
})
export class DeviceService {

  constructor(
    private injector: Injector,
    private deviceFactory: DeviceFactory,
    @Optional() @Inject(ConfigToken) private APP_CONFIG: any) {
    let env = this.APP_CONFIG.ENV;
    this.SSL_fingerprints = this.APP_CONFIG[env].SSL_FINGERPRINTS;
    if (env === 'DEV_WebSQL')
      this.initDeviceID().then((id) => {
        this.deviceID = id;
      });
  };

  private SSL_fingerprints = [];
  private _isPad: boolean;
  private deviceID: string = null;
  private mock_deviceID: string = uuid();

  public async initDeviceAPI() {
    window.open = cordova.InAppBrowser.open;
    this._isPad = this.judgePad();
    this.lockScreenOrient();
    this.disableBackBtn();
    this.deviceID = await this.initDeviceID();
  }
  public getDevicePlatform(): string {
    let platform = null;
    try {
      platform = device.platform;
    }
    catch (error) {
      console.log('cannot get platform, beacause maynot on device.');
      platform = 'PC';
      //this.errorHandler.handleError(error);
    }
    finally {
      return platform;
    }
  }

  public getDeviceUUID(): string {
    if (this.deviceID)
      return this.deviceID;
  }

  private _getDeviceUUID(): string {
    let uuid = null;
    try {
      uuid = device.uuid;
    }
    catch (error) {
      console.log('cannot get UUID, beacause maynot on device.');
      uuid = this.mock_deviceID;
      //this.errorHandler.handleError(error);
    }
    finally {
      return uuid;
    }

  }

  private async initDeviceID(): Promise<string> {
    let deviceID = this._getDeviceUUID();
    try {
      let resp = await this.getSecureStorage("deviceID").toPromise();
      console.log("getDeviceID from KeyStore resp:", resp);
      if (resp.includes("Device is not secure")) { //which Device is not secure
        throw new Error(resp);
      } else if (resp) { // if have key in keyStore/keyChain
        return resp;
      } else {
        let setKeyResp = await this.setSecureStorage("deviceID", deviceID).toPromise();
        if (setKeyResp) {
          let key = await this.getSecureStorage("deviceID").toPromise();
          return key;
        }
      }
    } catch (error) {
      console.log("getDeviceID error, use localStorage");
      if (this.getLocalStorage("deviceID")) {
        return this.getLocalStorage("deviceID");
      } else {
        this.setLocalStorage("deviceID", deviceID);
        return deviceID;
      }
    }
  }

  public getDeviceManufacturer(): string {
    let manufacturer = null;
    try {
      manufacturer = `${device.manufacturer} ${device.model}`;
    }
    catch (error) {
      console.log('cannot get Manufacturer, beacause maynot on device.');
      manufacturer = "PC Browser";
      // this.errorHandler.handleError(error);
    }
    finally {
      return manufacturer;
    }
  }

  public restartApp() {
    try {
      // navigator.splashscreen.show();
      window.location.reload(true);
      window.location.assign('index.html');
      // navigator.splashscreen.hide();
    }
    catch (error) {
      console.log('restart error');
      //this.errorHandler.handleError(error);
    }
  }

  public getNetworkState(): boolean {
    if (navigator && navigator.connection) {
      let networkState = navigator.connection.type;
      console.log('networkState:', networkState);
      return networkState != 'none';
    }
    else
      return true;
  }

  public async searchContactsByName(word: string) {
    let dao = this.deviceFactory.getDefaultDao();
    try {
      let resp = await dao.searchcontactsByName('').toPromise();
      console.log('search Contact result:', resp);
      let contacts = resp['Body'];

      for (let item of contacts) {
        if (StringUtils.isEmpty(item.l))
          item.isCheck = false;
      }
      return contacts;
    } catch (error) {
      console.log('error:', error);
      return error
    }
  }

  public dialNumber(num: string): void {
    window.open('tel:' + num.replace(/\s/g, ''), '_system');
  }

  public checkIfRoot(): Promise<boolean> {

    return new Promise((res, rej) => {
      try {
        console.log("iroot:", IRoot);
        if (IRoot !== undefined) {
          IRoot.isRooted((result) => {
            res(result);
          }, (error) => {
            rej(error);
          });
        }
        else {
          throw new Error("iRoot plugin error.");
        }
      } catch (error) {
        res(false);
      }
    })
  }

  public exitApp(): void {
    cordova.plugins.exit();
    // navigator.app.exitApp();
  }

  public getAppVersion(): Promise<string> {
    try {
      return cordova.getAppVersion.getVersionNumber();
    }
    catch (error) {
      //this.errorHandler.handleError(error);
      return Promise.resolve("1.0.3");//1.0.0
    }
  }

  public getIsFirstLaunch() {
    return this.getLocalStorage("AppFirstLaunch") === 'N' ? false : true;
  }

  public setLocalStorage(key: string, value: string) {
    window.localStorage.setItem(key, value);
  }

  public getLocalStorage(key: string): string {
    return window.localStorage.getItem(key);
  }



  private lockScreenOrient(): void {
    let orientation = this.isPad() ? 'landscape' : 'portrait-primary';
    (<any>window).screen.orientation.lock(orientation);
  }

  public judgePad(): boolean {
    let width = window.innerWidth;
    let height = window.innerHeight;
    console.log('width:', width);
    console.log('height:', height);
    if (width < 700 || height < 700) {
      return false;
    }
    else {
      return true;
    }
  }

  public isPad() {
    return this._isPad;
  }


  public setSecureStorage(key: string, value: string): Observable<boolean> {
    return Observable.create((observer) => {
      let ss = new cordova.plugins.SecureStorage(
        function () {
          ss.set(
            function () {
              observer.next(true);
              observer.complete();
            },
            function (error) {
              observer.next(false);
              observer.complete();
            },
            key,
            value
          );
        },
        function (error) {
          observer.next(false);
          observer.complete();
        },
        "Allianz_SND"
      );
    });
  }

  public getSecureStorage(key: string): Observable<string> {
    return Observable.create((observer) => {
      let ss = new cordova.plugins.SecureStorage(
        function () {
          ss.get(
            function (key) {
              observer.next(key);
              observer.complete();
            },
            function () {
              observer.next(null);
              observer.complete();
            },
            key
          )
        },
        function (error) {
          observer.next(error.message);
          observer.complete();
        },
        "Allianz_SND"
      )
    })
  }

  public checkSSL(url): Promise<any> {
    if (this.SSL_fingerprints.length == 0)
      return Promise.resolve(true);
    else
      return new Promise((res, rej) => {
        (<any>window).plugins.sslCertificateChecker.check(
          (message) => {
            if (message === 'CONNECTION_SECURE')
              res(true);
          },
          (error) => {
            rej(error);
          },
          url,
          this.SSL_fingerprints);
      })
  }

  public takeScreenShot() {
    let _this = this;
    (<any>navigator).screenshot.save(function (error, res) {
      if (error) {
        //todo throw error
        console.warn(error);
      } else {
        console.log('testScreenShot success', res.filePath);
        console.warn('getDevicePlatform', _this.getDevicePlatform());
        if (_this.getDevicePlatform() == "iOS")
          (<any>cordova).plugins.imagesaver.saveImageToGallery(res.filePath, function () { console.log("success") }, function (err) { console.log(err) });
      }
    });
  }

  public disableBackBtn() {
    document.addEventListener("backbutton", function (e) {
      e.preventDefault();
    }, false);
  }

  public checkContactPermission(): Promise<any> {
    return new Promise((resolve, reject) => {
      (<any>cordova).plugins.CheckPermission.checkContactPermission('contact',
        function (msg) {
          resolve(msg)
        },
        function (error) {
          reject(error);
        })
    });
  }

  public grantContactPermission() {
    (<any>cordova).plugins.CheckPermission.grantContactPermission()
  }

}
