import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { Observable } from 'rxjs';
import { DaoFactory } from '../../device/sqlite';
import {SQLiteTable} from '../../device/sqlite';
import {ClientCustomDao} from '../../device/sqlite';

export class CurrentLanguageListAPI implements IAPI , IMockAPI , ISQLiteAPI {
  private _DaoFactory;
  constructor(DaoFactory: DaoFactory) {
    this._DaoFactory = DaoFactory;
  }

  getAPIName() : string {
    return 'getCurrentLanguageList';
  }

  getMockPath() : string {
    return './assets/mock/getCurrentLanguage.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let languageObj = this._DaoFactory.getTable('Profile', "TW_LH_SD_Language");
      let dao = this._DaoFactory.getDao('Profile');

      if (languageObj != undefined && dao != undefined) {
        languageObj = (<SQLiteTable>languageObj);
        dao = new ClientCustomDao(dao);
        dao.queryByTable(languageObj).subscribe((resp) => {
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }

}
