import { IDao } from "./Dao.interface";
import { SQLiteTable } from "./SQLiteTable";
import { SQLiteColumn } from "./SQLiteColumn";
import { Observable, of } from "rxjs";
import { LimitRestriction } from "./restrictions/LimitRestriction";
import { OffsetRestriction } from "./restrictions/OffsetRestriction";
import { SQLiteResponse } from "./SQLiteResponse";
import { SQLCommand } from "./SQLCommand";
import { OrderByRestriction } from "./restrictions/OrderByRestriction";
import { DaoFactory } from "./DaoFactory";
import { APPError } from "../../errorHandler/APPError";
import { SQLiteDatabase } from "./SQLiteDatabase";

export class SQLiteDao implements IDao {
    protected connection: any;
    protected transactionArr: Array<any>;
    protected config: SQLiteDatabase;
    constructor(config: SQLiteDatabase) {
        this.transactionArr = [];
        this.config = config;
    }

    public openDataBase(): Promise<any> {
        let dbObj = this.config;
        return new Promise((res, rej) => {
            (<any>window).sqlitePlugin.openDatabase({
                name: dbObj.getName() + '.db',
                location: 'default',
                key: dbObj.getKey(),
                androidDatabaseImplementation: 'system'
            }, db => {
                this.connection = db;
                res(db);
            }, () => {
                let error = new Error("Sqlite DB open error.");
                rej(error);
            });
        })
    }

    public queryByTable(tableObject: SQLiteTable): Observable<SQLiteResponse> {
        var restrictions = tableObject.getRestrictions();
        var conditon_str = restrictions.filter(x => !(x instanceof LimitRestriction || x instanceof OffsetRestriction || x instanceof OrderByRestriction)).map(x => `AND ${x.sqlString()}`).join('');
        var conditon_val = restrictions.filter(x => !(x instanceof LimitRestriction || x instanceof OffsetRestriction || x instanceof OrderByRestriction)).map(x => x.getValues()).reduce((prev, x) => [...prev, ...x], []);
        var order_str = restrictions.filter(x => x instanceof OrderByRestriction).map(x => x.sqlString()).join('');
        var limit_str = restrictions.filter(x => x instanceof LimitRestriction).map(x => x.sqlString()).join('');
        var offset_str = restrictions.filter(x => x instanceof OffsetRestriction).map(x => x.sqlString()).join('');
        var sql_count_command = `SELECT count(*) FROM ${tableObject.getTableName()} WHERE 1=1 ${conditon_str}`;
        var sql_command = `SELECT * FROM ${tableObject.getTableName()} WHERE 1=1 ${conditon_str}${order_str}${limit_str}${offset_str}`;
        var sql_obj = new SQLCommand(sql_command, conditon_val);
        var sqlcount_obj = new SQLCommand(sql_count_command, conditon_val);
        console.group("run select sql command");
        console.log(sql_command);
        console.log('params', conditon_val);
        console.groupEnd();
        return this.runQuerySqlcommand(sql_obj, sqlcount_obj);
    }

    public insertByTable(tableObject: SQLiteTable): Observable<SQLiteResponse> {
        let sql_obj = this.transformInsert(tableObject);
        console.group("run insert sql command");
        console.log(sql_obj.getSql());
        console.log('params', sql_obj.getParams());
        console.groupEnd();
        return Observable.create((observer) => {
            this.runSqlCommand(observer, sql_obj);
        })
    }

    public updateByTable(tableObject: SQLiteTable): Observable<SQLiteResponse> {
        let sql_obj = this.transformUpdate(tableObject);
        console.group("run update sql command");
        console.log(sql_obj.getSql());
        console.log('params', sql_obj.getParams());
        console.groupEnd();
        return Observable.create((observer) => {
            this.runSqlCommand(observer, sql_obj);
        })
    }

    public deleteByTable(tableObject: SQLiteTable): Observable<SQLiteResponse> {
        let sql_obj = this.transformDelete(tableObject);
        console.group("run delete sql command");
        console.log(sql_obj.getSql());
        console.log('params', sql_obj.getParams());
        console.groupEnd();
        return Observable.create((observer) => {
            this.runSqlCommand(observer, sql_obj);
        })
    }

    public transactionInsert(tableObject: SQLiteTable) {
        this.addTransactionCommand(this.transformInsert(tableObject));
    }
    public transactionUpdate(tableObject: SQLiteTable) {
        this.addTransactionCommand(this.transformUpdate(tableObject));
    }
    public transactionDelete(tableObject: SQLiteTable) {
        this.addTransactionCommand(this.transformDelete(tableObject));
    }

    public transactionSqlCommand(command: SQLCommand) {
        this.addTransactionCommand(command);
    }

    public excuteSqlCommand(sql_command: Array<SQLCommand>) {
        console.log('excuteSqlCommand:', sql_command);
        return Observable.create((observer) => {
            this.connection.transaction(tr => {
                for (let command of sql_command) {
                    tr.executeSql(command.getSql(), command.getParams(), () => { }, (_, error) => {
                        console.log('execute sql error:', error);
                    });
                }
            }, (error) => {
                observer.next(new SQLiteResponse({ status: false, msg: error.message }, []));
                observer.complete();
                throw new APPError("F00017", error.message);
            }, () => {
                observer.next(new SQLiteResponse({ status: true }, []));
                observer.complete();
            })
        });
    }

    public getSchema() {
        var schema = new Map();
        return Observable.create((observer) => {
            this.connection.transaction(tr => {
                tr.executeSql('SELECT name,sql FROM sqlite_master WHERE type="table" OR type="view"', [], (tx, res) => {
                    //TABLEquery table schema
                    for (var len = 0; len < res.rows.length; len++) {
                        var table_name = res.rows.item(len).name;
                        var table_sql = res.rows.item(len).sql;
                        this.getColumns(tx, schema, table_name, table_sql);
                    }
                }, (tx, error) => {
                    console.log('select table name error:', error.message);
                });
            }, (_, error) => {
                observer.next(new SQLiteResponse({ status: false, msg: error.message }, []));
                observer.complete();
                throw new APPError("F00017", error.message);
            }, () => {
                observer.next(schema);
                observer.complete();
            });
        })

    }

    public runTransaction() {
        return Observable.create((observer => {
            let batchArr = this.transactionArr.map((cmd) => [cmd.getSql(), cmd.getParams()]);
            console.group('run transaction SQL:');
            console.log('command:', batchArr);
            console.groupEnd();
            this.connection.sqlBatch(batchArr, () => {
                observer.next(new SQLiteResponse({ status: true }, []));
                observer.complete();
            }, (error) => {
                observer.next(new SQLiteResponse({ status: false, msg: error.message }, []));
                observer.complete();
                throw new APPError("F00018", error.message);
            });
        }))
    }

    public clearTransaction() {
        this.transactionArr = [];
    }

    protected getColumns(tx, schema, table_name, table_sql) {
        var tmp_colArray = [];
        console.log("getColumns:", table_name, table_sql);
        tx.executeSql('PRAGMA table_info(' + table_name + ')', [], (tx, info_res) => {
            //table
            for (var i = 0; i < info_res.rows.length; i++) {
                var isPK = !!info_res.rows.item(i).pk;
                var name = info_res.rows.item(i).name;
                var isAutoIncrement = isPK && (table_sql.toUpperCase().indexOf("AUTOINCREMENT") > -1);
                var type = info_res.rows.item(i).type;
                tmp_colArray.push(new SQLiteColumn(name, undefined, type, isPK, isAutoIncrement));
            }
            var tableObj = new SQLiteTable(table_name, tmp_colArray);
            schema.set(table_name, tableObj);
        }, (tx, error) => {
            console.log('Get table column list error:', error.message);
            throw new APPError("F00019", error.message);
        });
    }

    private runSqlCommand(observer, sql_command) {
        this.connection.transaction(tr => {
            tr.executeSql(sql_command.getSql(), sql_command.getParams(), (tx, res) => {
                observer.next(new SQLiteResponse({ status: true }, []));
                observer.complete();
            }, (_, error) => {
                observer.next(new SQLiteResponse({ status: false, msg: error.message }, []));
                observer.complete();
                throw new APPError("F00017", error.message);
            });
        });
    }

    private runQuerySqlcommand(sql_obj, sql_count_obj) {
        console.log("runQuerySqlcommand", sql_obj);
        return Observable.create((observer) => {
            this.connection.transaction(tr => {
                tr.executeSql(sql_obj.getSql(), sql_obj.getParams(), (tx, res) => {
                    tx.executeSql(sql_count_obj.getSql(), sql_count_obj.getParams(), (tx, res_count) => {
                        var data = [];
                        for (var i = 0; i < res.rows.length; i++) {
                            data.push(res.rows.item(i));
                        }
                        console.log("run query sql finish!", data);
                        observer.next(new SQLiteResponse({
                            record_length: res_count.rows.item(0)['count(*)'],
                            status: true
                        }, data));
                        observer.complete();
                    }, (_, error) => {
                        console.log(error.message);
                        observer.next(new SQLiteResponse({ status: false, message: error.message }, []));
                        observer.complete();
                        throw new APPError("F00017", error.message);
                    })
                }, (_, error) => {
                    console.log(error.message);
                    observer.next(new SQLiteResponse({ status: false, message: error.message }, []));
                    observer.complete();
                    throw new APPError("F00017", error.message);
                });
            });
        })

    }

    private transformInsert(tableObject: SQLiteTable) {
        var setted_cols = tableObject.getColumns().filter(x => x.getValue() !== undefined);
        var setted_vals = setted_cols.map(x => x.getValue());
        var sql_command = `INSERT INTO ${tableObject.getTableName()} (${setted_cols.map(x => x.getName()).join(',')}) VALUES (${setted_cols.map(x => '?').join(',')})`;
        return new SQLCommand(sql_command, setted_vals);
    }

    private transformUpdate(tableObject: SQLiteTable) {
        var setted_cols = tableObject.getColumns().filter(x => x.getValue() !== undefined);
        var setted_vals = setted_cols.map(x => x.getValue());
        var set_str = setted_cols.map(x => ` ${x.getName()}=?`).join(',');
        var restrictions = tableObject.getRestrictions();
        var conditon_str = restrictions.filter(x => !(x instanceof LimitRestriction || x instanceof OffsetRestriction)).map(x => `AND ${x.sqlString()}`).join('');
        var conditon_val = restrictions.filter(x => !(x instanceof LimitRestriction || x instanceof OffsetRestriction)).map(x => x.getValues()).reduce((prev, x) => [...prev, ...x], []);
        var sql_command = `UPDATE ${tableObject.getTableName()} SET ${set_str} WHERE 1=1 ${conditon_str}`;
        return new SQLCommand(sql_command, [...setted_vals, ...conditon_val]);
    }

    private transformDelete(tableObject: SQLiteTable) {
        var restrictions = tableObject.getRestrictions();
        var conditon_str = restrictions.filter(x => !(x instanceof LimitRestriction || x instanceof OffsetRestriction)).map(x => `AND ${x.sqlString()}`).join('');
        var conditon_val = restrictions.filter(x => !(x instanceof LimitRestriction || x instanceof OffsetRestriction)).map(x => x.getValues()).reduce((prev, x) => [...prev, ...x], []);
        var sql_command = `DELETE FROM ${tableObject.getTableName()} WHERE 1=1 ${conditon_str}`;
        return new SQLCommand(sql_command, conditon_val);
    }

    private addTransactionCommand(sql_command: SQLCommand) {
        this.transactionArr.push(sql_command);
    }


}
