import { IRestriction } from '../Restriction.interface';
export class AndCompoundRestriction implements IRestriction {
    private restrictions : Array<IRestriction>;    
    constructor(restrictions) {
        this.restrictions = restrictions;
    }
    sqlString(): string {

        let sqlString = '';
        
        this.restrictions.forEach(function(restriction , i){
            if(i != 0)  sqlString += ' AND ';
            sqlString += '' + restriction.sqlString();
        });

        return `(`+sqlString+`)`;
    }

    getValues() {
        let values = new Array<string>();
        this.restrictions.forEach(function(restriction , i){
            let array = restriction.getValues();
            values = values.concat(array);
        });

        return values;
    }
}