
import { Observable, of } from "rxjs";
import { IAPI } from "../API.interface";
import { IMockAPI } from "../MockAPI.interface";
import { ISQLiteAPI } from "../SQLiteAPI.interface";
import { ClientCustomDao, DaoFactory } from '../../device/sqlite';
import { EqualRestriction } from '../../device/sqlite';
import { IRestfulAPI } from "../RestfulAPI.interface";
import { APIRequest } from "../APIRequest";

export class UnbindDeviceAPI implements IAPI, IMockAPI, IRestfulAPI {

    constructor(private APP_CONFIG: any) {
    }


    private _deviceID: string = '';

    setDeviceID(deviceID: string) {
        this._deviceID = deviceID;
    }

    getAPIName(): string {
        return 'unbindDevice';
    }

    getMockPath(): string {
        return './assets/mock/logout.json';
    }

    getRequestData(): APIRequest {
        let env = this.APP_CONFIG.ENV;
        let cleanDeviceUrl = this.APP_CONFIG[env]['API_URL']['unbindDevice'];
        let requestData = new APIRequest();
        requestData.url = cleanDeviceUrl;
        requestData.body = { "DeviceId": this._deviceID };
        requestData.type = "POST";
        return requestData;
    }
}
