import { IMetaExecutor } from "../MetaExecutor.interface";
import { MetaConfig } from "../../parser/bean/MetaConfig";
import { Injectable, ErrorHandler } from "@angular/core";
import { APPError } from "../../../errorHandler/APPError";
import { APIFactory } from "../../../api/APIFactory";
import { APIDispatch } from "../../../api/APIDispatch";


@Injectable({
    providedIn: 'root'
})
export class APIExecutor implements IMetaExecutor {

    constructor(
        private errorHandler: ErrorHandler,
        private APIFactory: APIFactory,
        private dispatcher: APIDispatch
    ) { }

    public async getData(config: MetaConfig, params: any = null) {
        try {
            let API = this.APIFactory.getAPI(config.source.read);
            if (params)
                Object.keys(params).forEach((key) => {
                    API[key] = params[key];
                })
            let resp = await this.dispatcher.dispatch(API).toPromise();
            return resp["Body"][0];

        } catch (error) {
            this.errorHandler.handleError(new APPError("F00802", error.message));
            return null;
        }
    }

    public async saveData(config: MetaConfig, data: any, params: any = null) {
        try {

            let API = this.APIFactory.getAPI(config.source.write);
            if (params)
                Object.keys(params).forEach((key) => {
                    API[key] = params[key];
                })
            API["Data"] = data;
            return await this.dispatcher.dispatch(API).toPromise();

        } catch (error) {
            this.errorHandler.handleError(new APPError("F00803", error.message));
            return null;
        }
    }
}