import { ValidCondition } from "./condition/ValidCondition.interface";

export class ValidProperty {
    private _name: string;
    public get name(): string {
        return this._name;
    }
    public set name(value: string) {
        this._name = value;
    }
    private conditionList: Array<ValidCondition>;

    constructor(name: string) {
        this.name = name;
        this.conditionList = [];
    }
    public addCondition(condition: ValidCondition) {
        this.conditionList.push(condition);
    }

    public validProperty(value): Array<string> {
        return this.conditionList.filter(x => !x.valid(value)).map(x => x.getErrorMsg(this.name));
    }
}