import { HttpClient } from "@angular/common/http";
import { IAPI } from "../API.interface";
import { IRestfulAPI } from "../RestfulAPI.interface";
import { IMockAPI } from "../MockAPI.interface";
import { APIRequest } from "../APIRequest";

export class LoginAPI implements IAPI, IRestfulAPI, IMockAPI {

  constructor() { }

  public url = '';
  public body = {};
  public type = "GET";
  public params = null;

  public getAPIName() {
    return 'onlineLogin';
  }

  public getMockPath() {
    return './assets/mock/onlineLogin.json';
  }

  public getRequestData() {
    let requestData = new APIRequest();
    requestData.body = this.body;
    requestData.url = this.url;
    requestData.type = "POST";
    requestData.params = this.params;
    return requestData;
  }

}

