import { Injectable, Optional, Inject, ErrorHandler } from '@angular/core';
import { IInitTask } from '../interface/InitialTask.interface';
import { DeviceService } from '../../device/device.service';
import { checkRoot } from '../interface/checkRoot.interface';
import { checkForRootToken, NetworkChangeToken } from '../../injectionToken';
import { fromEvent } from 'rxjs';
import { NetworkChange } from '../../device/NetworkChange.interface';


@Injectable({
    providedIn: "root"
})
export class DeviceTask implements IInitTask {

    constructor(
        private deviceService: DeviceService,
        private errorHandler: ErrorHandler,
        @Optional() @Inject(checkForRootToken) private customCheckRoot: checkRoot,
        @Optional() @Inject(NetworkChangeToken) private networkChange: NetworkChange
    ) { }

    public doTask(): Promise<any> {
        console.log('do device task');
        return new Promise((res, rej) => {
            //do task
            //check for root
            console.log('has inject custom root:', this.customCheckRoot);
            try {
                if (this.customCheckRoot) {
                    this.customCheckRoot.checkRoot().then((resp) => {
                        if (resp)
                            this.customCheckRoot.rootedAction();
                        res();
                    })
                }
                else {
                    this.deviceService.checkIfRoot().then(resp => {
                        console.log('check root resp:', resp);
                        if (resp) {
                            alert("device has rooted!");
                            this.deviceService.exitApp();
                        }
                        res();
                    }, errMsg => {
                        console.warn(errMsg);
                    })
                }

                //device network event
                fromEvent(document, 'online').pipe(
                    // debounceTime(5000)
                ).subscribe(() => {
                    this.networkChange.change('online',true);
                })
                fromEvent(document, 'offline').pipe(
                    // debounceTime(5000)
                ).subscribe(() => {
                    this.networkChange.change('offline',true);
                })
            } catch (error) {
                console.warn("cannot detect rooted device.");
                this.errorHandler.handleError(error);
                res();
            }

        });
    }
}