import { SQLiteColumn } from './SQLiteColumn';
import { IRestriction } from './Restriction.interface';

export class SQLiteTable {
  private tableName: string;
  private columns: Array<SQLiteColumn>;
  private restrictions: Array<IRestriction>;
  constructor(name: string, columns: Array<SQLiteColumn>) {
    this.tableName = name;
    this.columns = columns;
    this.restrictions = [];
  }
  setValue(col: string, value: any) {
    this.columns.filter(x => x.getName() == col).forEach((col) => {
      col.setValue(value);
    })
  }
  getValue(col: string) {
    let cols = this.columns.filter(x => x.getName() == col);
    return col.length > 0 ? cols[0].getValue() : null;
  }

  hasColumn(col: string) {
    let cols = this.columns.filter(x => x.getName() == col);
    return cols.length > 0;
  }

  getTableName() {
    return this.tableName;
  }
  getColumns() {
    return this.columns;
  }

  addRestriction(restriction: IRestriction) {
    this.restrictions.push(restriction);
  }
  getRestrictions() {
    return this.restrictions;
  }

  cloneTable(): SQLiteTable {
    var colArray = [];
    for (var column of this.columns) {
      var clone_obj = Object.create(SQLiteColumn.prototype);
      clone_obj = Object.assign(clone_obj, column);
      colArray.push(clone_obj);
    }
    return new SQLiteTable(this.tableName, colArray);
  }

  isSetted() {
    return this.getColumns().map(x => x.getValue() !== undefined).length > 0;
  }

}
