import { Injectable, Optional, Injector } from "@angular/core";
import { Router, NavigationEnd, RouteReuseStrategy } from "@angular/router";
import { APIFactory } from "../api/APIFactory";
import { APIDispatch } from "../api/APIDispatch";
import { RouteUtils } from "../utils/routeUtils";
import { CustomRouterReuseStrategy } from "./customRouterReuseStrategy";

@Injectable({
    providedIn: "root"
})
export class AppRouter {

    private routeMap: Object = {};
    private currentMenuObject: any = null;
    private previousUrl: string = null;
    private currentUrl: string = null;

    constructor(
        private injector: Injector,
        private APIFactory: APIFactory,
        private dispatcher: APIDispatch,
        private routeReuseStrategy: RouteReuseStrategy
    ) {
    }

    public init() {
        //call api to get route map
        let routerMapAPI = this.APIFactory.getAPI("getRouterMap");
        if (routerMapAPI) {
            this.dispatcher.dispatch(routerMapAPI).subscribe((map) => {
                console.log("routeMapAPI:", map);
                this.routeMap = RouteUtils.convertJsonToRouterMap(map);
                console.log("router map:", this.routeMap);
            })
        }
        this.listenRouterEvent();
    }

    public navigate(func: string, ...extras: Array<string>) {
        let router = this.injector.get(Router);
        console.log("navigate:", router, func, this.routeMap[func].Path);
        if (router && this.routeMap[func]) {
            if ((<any>window).FirebasePlugin)
                (<any>window).FirebasePlugin.setScreenName(this.routeMap[func].Function);
            // this.currentFunction = this.routeMap[func].Function;
            if (extras)
                router.navigate([this.routeMap[func].Path, ...extras]);
            else
                router.navigate([this.routeMap[func].Path]);
        }
    }

    public getUrl(page: string) {
        return this.routeMap[page] ? this.routeMap[page].Path : '';
    }

    public getCurrentFunction() {
        let router = this.injector.get(Router);
        let filtered = Object.keys(this.routeMap).filter(x => router.isActive(this.routeMap[x].Path, false))
            .map(x => this.routeMap[x].Function);
        if (filtered.length > 0) {
            return filtered[0];
        }
        else {
            return "Not Found";
        }
    }
    public getRouteMap(): any {
        return this.routeMap;
    }

    public back(clearCache: boolean = true) {
        console.log("router back to:", this.previousUrl);
        if (this.previousUrl) {
            if (clearCache && this.routeReuseStrategy["deleteRouteSnapshot"]) {
                (<CustomRouterReuseStrategy>this.routeReuseStrategy).deleteRouteSnapshot();
            }
            let router = this.injector.get(Router);
            router.navigate([this.previousUrl]);
        }
    }

    public getCurrentMenuObject(): any {
        return this.currentMenuObject;
    }

    public setCurrentMenuObject(val: any): void {
        this.currentMenuObject = val;
    }

    private listenRouterEvent() {
        let router = this.injector.get(Router);
        router.events.subscribe(event => {
            if (event instanceof NavigationEnd) {
                this.previousUrl = this.currentUrl;
                this.currentUrl = event.url;
            };
        });
    }
}
