import { IRestriction } from '../Restriction.interface';
export class GreaterRestriction implements IRestriction {
    private column: string;
    private values: Array<string>;
    constructor(column, values = []) {
        this.column = column;
        this.values = values;
    }
    sqlString(): string {
        return `${this.column} > ? `;
    }
    getValues() {
        return this.values;
    }
}