
import { HttpHeaders } from "@angular/common/http";
import { Injectable, Optional, Inject } from "@angular/core";
import { DefaultLoginMgr } from "../auth/login/DefaultLoginMgr";
import { ILoginMgr } from "../auth/login/LoginMgr.interface";
import { RestHeader } from "./RestHeader.interface";
import { DeviceService } from "../device/device.service";
import { LoginMgrToken } from "../injectionToken/injection-token";
import { LoginTokenStore } from "../auth/login/LoginTokenStore";

@Injectable({
    providedIn: 'root'
})
export class defaultHeader implements RestHeader {

    private token = '';
    private version = '';
    constructor(
        private deviceService: DeviceService,
        private tokenStore: LoginTokenStore,
        @Optional() @Inject(LoginMgrToken) private customLoginMgr: ILoginMgr
    ) {
        if (this.customLoginMgr) {
            this.customLoginMgr.getToken().subscribe((token) => {
                this.token = token;
            })
        }
        else {
            this.tokenStore.getToken().subscribe((token) => {
                this.token = token;
            })
        }
        this.deviceService.getAppVersion().then((ver) => {
            this.version = ver;
        })
    }

    getHeader(): HttpHeaders {
        return new HttpHeaders({
            'Content-Type': 'application/json',
            'Authorization': "Bearer " + this.token,
            'X-Date': new Date().toUTCString(),
            'X-Request-ID': Date.now().toString(),
            'X-Organization': '1',
            'X-Organization-Branch': '1',
            'X-Organization-User': '1',
            'X-API-Version': this.version,
        })
    }
}