import { Injectable, InjectionToken } from "@angular/core";

@Injectable({
    providedIn: 'root'
})
export class AOPTokenService {
    private tokenMap: Map<string, InjectionToken<any>>;
    constructor() {
        this.tokenMap = new Map();
    }

    registerToken(name: string, token: InjectionToken<any>) {
        this.tokenMap.set(name, token);
    }

    getToken(name: string): InjectionToken<any> {
        return this.tokenMap.get(name);
    }
}