import { IRestriction } from '../Restriction.interface';
export class OrderByRestriction implements IRestriction {
    private values: Array<any>;
    constructor(values = []) {
        this.values = values;
    }
    sqlString(): string {
        let str = this.values.map(x => `${x.column} ${x.order}`).reduce((prev, current) => prev + ',' + current);
        return `ORDER BY ${str} `;
    }

    getValues() {
        return this.values;
    }
}