import { IAPI } from "../API.interface";
import { IMockAPI } from "../MockAPI.interface";
import { ISQLiteAPI } from "../SQLiteAPI.interface";
import { Setting } from "../../bean";
import { Observable } from "rxjs";
import { DaoFactory, SQLiteTable, ClientCustomDao, EqualRestriction } from "../../device";

export class SettingUpdateAPI implements IAPI, IMockAPI, ISQLiteAPI {

    constructor(private DaoFactory: DaoFactory) { }

    private _settingObject: Setting;

    setSettingObject(value: Setting) {
        this._settingObject = value;
    }

    getAPIName(): string {
        return 'updateSetting';
    }

    getMockPath(): string {
        return './assets/mock/getPersonalSetting.json';
    }

    executeSQL(): Observable<any> {
        return Observable.create((observer) => {
            let settingObj = this.DaoFactory.getDefaultTable("TW_LH_SD_Personal_Setting");
            let dao = this.DaoFactory.getDefaultDao();

            if (settingObj != undefined && dao != undefined) {
                settingObj = (<SQLiteTable>settingObj);
                dao = new ClientCustomDao(dao);
                settingObj.addRestriction(new EqualRestriction('SettingID', [this._settingObject.SettingID]));
                settingObj.setValue('SettingVal', this._settingObject.SettingVal);

                dao.updateByTable(settingObj).subscribe((resp) => {
                    observer.next(resp);
                    observer.complete();
                });
            } else {
                observer.next(false);
                observer.complete();
            }
        });
    }

}
