export class ValidationResult{
    private errorMap : Map<string,string> = new Map<string,string>();

    setErrorMap(name: string,errorMsg: string) {
      this.errorMap.set(name, errorMsg);
    }

    deleteError(name: string) {
      this.errorMap.delete(name);
    }

    isTrue() : boolean {
      return (this.errorMap.size == 0)
    }

    isError(name : string) : boolean {
        return this.errorMap.get(name) != undefined;
    }

    getErrorMsg(name:string) : string {
        return this.errorMap.get(name);
    }

    clearErrorMap() {
      this.errorMap.clear();
    }
}
