import { IAPI } from '../API.interface';
import { IRestfulAPI } from '../RestfulAPI.interface';
import { APIRequest } from '../APIRequest';
import { HttpParams } from '@angular/common/http';
import { IMockAPI } from '../MockAPI.interface';
import { SubmitGoalData } from '../../bean';


export class pushGoalSettingDataAPI implements IAPI, IMockAPI, IRestfulAPI {

    public url: string = '';
    public mainData: SubmitGoalData;

    constructor() { }

    getAPIName() {
        return 'pushGoalSettingData';
    }

    getRequestData() {
        let requestData = new APIRequest();
        requestData.url = this.url;
        requestData.body = this.mainData;
        requestData.type = "POST";
        return requestData;
    }

    getMockPath() {
        return './assets/mock/GoalCallback.json';
    }
}