import { BaseMetaComponent } from './BaseMetaComponent';
import { MetaService } from '../Meta.service';
import { ProfileCodeService } from '../../service/profile/profile-code.service';
import { IMetaParser } from '../parser/MetaParser.interface';
import { IMetaExecutor } from '../executor/MetaExecutor.interface';

export abstract class DisplayMetaComponent extends BaseMetaComponent {

    constructor(
        metaService: MetaService,
        protected profileCodeService: ProfileCodeService,
        parser: IMetaParser,
        executor: IMetaExecutor
    ) {
        super(metaService, parser, executor);
    }

    public ngOnInit() {
        console.log("DisplayMetaComponent ngOninit!");
        super.ngOnInit();
    }


    protected onDataUpdated() {
        this.convertCodeToDisplay();
    }

    private convertCodeToDisplay() {
        let needToConvert = this.metaConfig.Columns.filter(x => x.type === 'Label' && !!x.optionRef);
        needToConvert.forEach((col) => {
            if (this._data[col.id])
                this._data[col.id] = this.profileCodeService.convertCode2Text(col.optionRef, this._data[col.id]);
        })

        this.metaConfig.Columns.filter(x => x.type === 'Group').forEach(group => {
            let colsNeedConvert = group.groupColumns.filter(x => x.type === 'Label' && !!x.optionRef);
            console.log("colsNeedConvert:", colsNeedConvert);
            if (colsNeedConvert.length > 0 && this._data[group.id]) {
                this._data[group.id].forEach((groupData, index) => {
                    let extendedObj = colsNeedConvert.map(x => {
                        return {
                            key: x.id,
                            value: this.profileCodeService.convertCode2Text(x.optionRef, this._data[group.id][index][x.id])
                        }
                    }).reduce((acc, cur) => { acc[cur.key] = cur.value; return acc; }, {});
                    console.log("extendedObj:", extendedObj);
                    groupData = Object.assign(groupData, extendedObj);
                });
            }
        })

    }
}