import { MetaService } from "../Meta.service";
import { IMetaExecutor } from "../executor/MetaExecutor.interface";
import { IMetaParser } from "../parser/MetaParser.interface";
import { MetaConfig } from "../parser/bean/MetaConfig";
import { APPError } from "../../errorHandler/APPError";
export abstract class BaseMetaComponent {

    constructor(
        protected metaService: MetaService,
        protected parser: IMetaParser,
        protected executor: IMetaExecutor
    ) {
        this.metaID = this.getMetaID();
    }

    protected metaID: string = null;
    protected metaLoaded: boolean = false;


    protected metaConfig: MetaConfig = null;
    protected _data: any = null;

    protected abstract getMetaID(): string;
    protected abstract getMetaParams(): any;

    public ngOnInit() {
        console.log("MetaComponent ngOninit!");
        this.init();
    }

    private async init() {
        try {
            let metaJSON = await this.metaService.getMetaJson(this.metaID);
            this.metaConfig = this.parser.parse(metaJSON);
            await this.loadData();
            this.metaLoaded = true;
        } catch (error) {
            throw new APPError("F00800", error.message);
        }
    }

    protected async loadData() {
        let executed_data = await this.executor.getData(this.metaConfig, this.getMetaParams());
        if (executed_data) {
            this._data = executed_data;
            this.onDataUpdated();
        }
    }

    protected waitUntilMetaLoaded(): Promise<any> {
        return new Promise((res, rej) => {
            let checkInterval = setInterval(() => {
                if (this.metaLoaded) {
                    clearInterval(checkInterval);
                    res();
                }
            }, 100);
        });
    }

    public isMetaLoaded() {
        return this.metaLoaded;
    }

    protected abstract onDataUpdated();

}