export class StringUtils {
  private static readonly matchEnglishRegExp = "[a-zA-Z]+";
  private static readonly matchChineseRegExp = "[\\u4e00-\\u9fa5]+|['']+";
  private static readonly matchNumberRegExp = "[0-9]+";
  constructor() {
  }

  public static isNotEmpty(val): boolean {
    return val !== '' && val !== null && val !== undefined && val !== 'null' && val !== 'NULL';
  }

  public static trim(val): string {
    if (this.isEmpty(val)) return '';
    else return val.trim();
  }

  public static isEmpty(val): boolean {
    if (this.isNotEmpty(val))
      return false;
    else
      return true;
  }

  public static convertEmptyVal(val): string {
    if (this.isNotEmpty(val))
      return val;
    else
      return '';
  }

  public static byteLength(val): number {
    let byteLen = 0;
    if (val && val.length) {
      for (let i = 0; i < val.length; i++) {
        if ((val.charCodeAt(i) & 0xff00) != 0) {
          byteLen++;
        }
        byteLen++;
      }
    }
    return byteLen;
  }

  public static isHasEnglish(val: string): boolean {
    return !!val && val.match(this.matchEnglishRegExp) !== null;
  }

  public static isAllEnglish(val: string): boolean {
    let result = val && val.match(this.matchEnglishRegExp);
    return !!result && result[0].length === val.length;
  }

  public static isHasChinese(val: string): boolean {
    return !!val && val.match(this.matchChineseRegExp) !== null;
  }

  public static isAllChinese(val: string): boolean {
    let result = val && val.match(this.matchChineseRegExp);
    return !!result && result[0].length === val.length;
  }

  public static getEnglishCharCount(val: string): number {
    let result = val && val.match(this.matchEnglishRegExp);
    return result ? result[0].length : 0;
  }

  public static getChineseCharCount(val: string): number {
    let result = val && val.match(this.matchChineseRegExp);
    return result ? result[0].length : 0;
  }

  public static getNumberCharCount(val: string): number {
    let result = val && val.match(this.matchNumberRegExp);
    return result ? result[0].length : 0;
  }

  public static isHasThai(val: string): boolean {
    if (!val) {
      val = '';
    }
    let count = 0;
    val.split('').forEach(item => {
      if (item.charCodeAt(0) > 3584 && item.charCodeAt(0) < 3711) {
        count++;
      }
    });

    return count > 0;

  }


}
