import { Injectable, Optional, Inject } from '@angular/core';
import { IInitTask } from '../interface/InitialTask.interface';
import { TranslateService } from '../../language/translate.service';
import { DeviceService } from '../../device/device.service';
import {
    fetchSettingFinishToken,
    changeFontSizeToken,
    FontSizeAccessToken,
    ConfigToken
} from '../../injectionToken';
import { CheckPermissionService } from '../../auth/permission/check-permission.service';
import { fetchSettingFinish } from '../interface/fetchSettingFinish.interface';
import { SettingService } from '../../service/setting/setting.service';
import { changeFontSize } from '../../interface/changeFontSize.interface';
import { IFontSizeAccess } from '../../interface';
import { AppRouter } from '../../route/AppRouter';
import { NotificationMgr } from '../../notification/NotificationMgr';
import { APPMODE } from '../../bean';
import { APPError } from '../../errorHandler/APPError';
import { NotificationType } from '../../notification/INotificationProvider.interface';

declare var StatusBar;
declare var cordova;

@Injectable({
    providedIn: "root"
})
export class ConfigTask implements IInitTask {

    constructor(
        private translateService: TranslateService,
        private deviceService: DeviceService,
        private settingService: SettingService,
        private appRouter: AppRouter,
        private notificationMgr: NotificationMgr,
        @Inject(ConfigToken) private APP_CONFIG,
        @Optional() @Inject(fetchSettingFinishToken) private fetchSettingFinish: fetchSettingFinish,
        @Optional() @Inject(changeFontSizeToken) private changeFontSize: changeFontSize,
        @Optional() @Inject(FontSizeAccessToken) private fontSizeAccess: IFontSizeAccess,
        private checkPermissionService: CheckPermissionService) { }
    public async doTask(): Promise<any> {
        console.log('do task');
        var _this = this;
        //do task
        document.addEventListener("deviceready", function () {
            _this.deviceService.initDeviceAPI();
            if ((<any>cordova).plugins.AndroidFontSize) {
                (<any>cordova).plugins.AndroidFontSize.removeSystemFontSize();
            }
            if (_this.deviceService.getDevicePlatform() == 'Android') {
                document.addEventListener('screenshotDetect', function () {
                    alert('Screenshot');
                }, false)
            } else if (_this.deviceService.getDevicePlatform() == 'iOS') {
                document.addEventListener("screenshot", function () {
                    _this.notificationMgr.pushNotification(NotificationType.ScreenShotAlert, {});
                }, false);
            }
            // StatusBar.overlaysWebView(false);
            // StatusBar.styleBlackTranslucent();
            // StatusBar.show();
        }, false);

        this.integrationCheck();
        this.checkPermissionService.init();
        this.appRouter.init();
        await Promise.all([this.fetchLanguage(), this.fetchLanguageList(), this.fetchSetting(), this.loadLanguageFile()]);

    }

    private fetchLanguage(): Promise<any> {
        console.log("fetch language func");
        return new Promise((res, rej) => {
            this.translateService._fetchCurrentLanguage().subscribe((resp) => {
                console.log("_fetchCurrentLanguage resp:", resp);
                res(resp);
            });
        });
    }

    private fetchLanguageList(): Promise<any> {
        console.log("fetch language List");
        return new Promise((res, rej) => {
            this.translateService._fetchCodeData().subscribe((resp) => {
                res(resp);
            });
        })
    }

    private integrationCheck() {
        try {
            let needCheckKey = ['SNDToken', 'SNDLanguage'];
            if (this.APP_CONFIG.APP_MODE === APPMODE.Integration) {
                // Check LocalStorage
                let result = needCheckKey
                    .filter(key => !this.deviceService.getLocalStorage(key))
                    .map(key => `Integration mode failed: ${key} not found in localstorage. Please set ${key} into localstorage before switch to S&D.`);
                if (result.length > 0)
                    throw new Error(result[0]);
            }
        } catch (error) {
            alert('Integration failed. Please check console for more details.');
            throw new APPError('F00704', error.message);
        }

    }

    private fetchSetting(): Promise<any> {
        console.debug('fetch setting');
        return new Promise((res, rej) => {

            this.settingService._fetchCodeData().subscribe((resp) => {
                if (this.changeFontSize) {
                    if (this.fontSizeAccess) {
                        this.fontSizeAccess.getFontSize().subscribe((resp) => {
                            this.changeFontSize.updateFontSize(resp);
                        })
                    }
                }

                if (this.fetchSettingFinish) {
                    this.fetchSettingFinish.fetchSettingFinish(resp);
                }
                res(resp);
            });
        })

    }

    private async loadLanguageFile(): Promise<any> {
        await this.translateService.init();
    }
}
