export class SQLiteColumn {
  private colName: string;
  private colValue: any;
  private isPrimaryKey: boolean;
  private isAutoincrement: boolean;
  private type: string;
  constructor(name: string, value: string, type: string, isPrimaryKey: boolean, isAutoincrement: boolean) {
    this.colName = name;
    this.colValue = value;
    this.isPrimaryKey = isPrimaryKey;
    this.isAutoincrement = isAutoincrement;
    this.type = type;
  }

  getName() {
    return this.colName;
  }

  getValue() {
    return this.colValue;
  }

  getType() {
    return this.type;
  }

  setValue(val: any) {
    if (this.type == 'text')
      this.colValue = val.toString();
    else if (this.type == 'integer')
      this.colValue = parseInt(val);
    else if (this.type == 'real')
      this.colValue = parseFloat(val);
    else
      this.colValue = val;
  }

  getIsPrimaryKey() {
    return this.isPrimaryKey;
  }

  getIsAutoincrement() {
    return this.isAutoincrement;
  }
}
