import { ErrorHandler, Injectable, Injector, Inject } from '@angular/core';
import * as stacktrace from 'stacktrace-js';
import { APIFactory } from '../api/APIFactory';
import { APIDispatch } from '../api/APIDispatch';
import { logErrorAPI } from '../api/register/logErrorAPI';
import { DeviceService } from '../device/device.service';
import { ConfigToken } from '../injectionToken/injection-token';
import { SettingService } from '../service/setting/setting.service';

@Injectable()
export class ErrorsHandler implements ErrorHandler {

    private isDebug: boolean = false;

    constructor(
        private APIFactory: APIFactory,
        private dispatcher: APIDispatch,
        private deviceService: DeviceService,
        private settingService: SettingService,
        private injector: Injector,
        @Inject(ConfigToken) private APP_CONFIG: any
    ) {
        this.settingService.getDebugMode().subscribe(debug => {
            this.isDebug = debug;
        })
    }

    async handleError(error: Error) {
        let env = this.APP_CONFIG.ENV;
        let APIFactory = this.getAPIFactory();
        let dispatcher = this.getAPIDispatcher();
        // Log the error anyway
        console.error(error);
        if (this.isDebug)
            alert('error:' + error.message);
        let stack = await stacktrace.fromError(error, { offline: true });
        stack = stack.map(x => x.source);
        let logErrorAPI = APIFactory.getAPI("LogError");
        if (logErrorAPI) {
            (<logErrorAPI>logErrorAPI).time = new Date();
            (<logErrorAPI>logErrorAPI).message = error.message;
            (<logErrorAPI>logErrorAPI).stack = stack;
            (<logErrorAPI>logErrorAPI).DeviceModel = this.deviceService.getDeviceManufacturer();
            (<logErrorAPI>logErrorAPI).DeviceSystem = this.deviceService.getDevicePlatform();
            dispatcher.dispatch(logErrorAPI).toPromise().then((resp) => {
                console.log("log error resp:", resp);
            })
        }
    }

    private getAPIFactory() {
        return this.APIFactory || this.injector.get(APIFactory);
    }

    private getAPIDispatcher() {
        return this.dispatcher || this.injector.get(APIDispatch);
    }
}