
import { IAPI } from "../API.interface";
import { IMockAPI } from "../MockAPI.interface";

export class MetaConfigAPI implements IAPI, IMockAPI {

    private _configName = '';
    public get configName() {
        return this._configName;
    }
    public set configName(value) {
        this._configName = value;
    }


    getAPIName(): string {
        return 'getMetaConfig';
    }

    getMockPath() {
        return `./assets/config/meta/${this._configName}.json`;
    }

}
