export class Phone {
    private _type: string;
    private _number: string;
    public get number(): string {
        return this._number;
    }
    public set number(value: string) {
        this._number = value;
    }
    public get type(): string {
        return this._type;
    }
    public set type(value: string) {
        this._type = value;
    }

    constructor(type, number) {
        this._type = type;
        this._number = number;
    }

}