import { IMetaExecutor } from "../MetaExecutor.interface";
import { MetaConfig } from "../../parser/bean/MetaConfig";
import { Injectable } from "@angular/core";
import { APIExecutor } from "./APIExecutor";

@Injectable({
    providedIn: 'root'
})
export class InputExecutor extends APIExecutor implements IMetaExecutor {

    public async getData(config: MetaConfig, params: any) {
        return null;
    }
    public async saveData(config: MetaConfig, data: any, params: any) {
        if (config.source.write)
            return super.saveData(config, data, params);
        else
            return null;
    }
}