import { ValidCondition, ValidType } from "./ValidCondition.interface";
import { ValidController } from "../ValidController";

export class RequiredCondition implements ValidCondition {
    private type: ValidType;
    private value: any;
    private baseTypes = ['string', 'undefined', 'number', 'boolean'];

    constructor() {
        this.type = ValidType.Required;
    }

    getType(): ValidType {
        return this.type;
    }
    valid(value): boolean {
        this.value = value;
        console.log('RequiredCondition valid:', value, this.baseTypes.indexOf(typeof value) === -1);
        let isBaseType = this.baseTypes.indexOf(typeof value) !== -1 || value === null;
        if (isBaseType)
            return value !== null && value !== undefined;
        else if (value instanceof Array)
            return value.length > 0;
        else if (typeof value === 'object')
            return ValidController.validObject(value, value.beanName);
    }

    getErrorMsg(column: string): string {
        return `${column} is ${this.value} but expect required.`;
    }
}