export class SQLiteDatabase {
  private dbName : string;
  private key : string;

  constructor(name, key) {
    this.dbName = name;
    this.key = key;
  }
  getName() {
    return this.dbName;
  }
  getKey() {
    return this.key;
  }
  setName(name : string) {
    this.dbName = name
  }
  setkey(key : string) {
    this.key = key
  }
}
