import { Injectable } from '@angular/core';
import { IAPIAccess } from '../APIAccess.interface';
import { IAPI } from '../API.interface';
import { APIResponse } from '../APIResponse';
import { of, Observable } from 'rxjs';
import { ISQLiteAPI } from '../SQLiteAPI.interface';

@Injectable({
    providedIn: 'root'
})
export class SQLiteAPIAccess implements IAPIAccess {
    access(api: IAPI): Observable<any> {
        console.log('sqlite access');
        return (<ISQLiteAPI>api).executeSQL();
    }
}