import { Observable } from 'rxjs';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { DaoFactory } from '../../device/sqlite/DaoFactory';
import { SQLiteTable } from '../../device/sqlite/SQLiteTable';
import { ClientCustomDao } from '../../device/sqlite/ClientCustomDao';

export class UpdateTimeListAPI implements IAPI, IMockAPI, ISQLiteAPI {
  private _DaoFactory: DaoFactory;
  constructor(DaoFactory) {
    this._DaoFactory = DaoFactory;
  }

  getAPIName(): string {
    return 'getUpdateTimeList';
  }

  getMockPath(): string {
    return "./assets/mock/getUpdateTimeList.json";
  }


  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let dataSyncObj = this._DaoFactory.getTable('Profile', "TW_LH_SD_VW_FuncSync_Time");
      let dao = this._DaoFactory.getDao('Profile');
      if (dataSyncObj != undefined && dao != undefined) {
        dataSyncObj = (<SQLiteTable>dataSyncObj);
        dao = new ClientCustomDao(dao);
        dao.queryByTable(dataSyncObj).subscribe((resp) => {
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
