import { IAPI } from "../API.interface";
import { IMockAPI } from "../MockAPI.interface";
import { IRestfulAPI } from "../RestfulAPI.interface";
import { APIRequest } from "../APIRequest";
import { HttpParams } from "@angular/common/http";

export class CheckVersionAPI implements IAPI, IMockAPI, IRestfulAPI {

    constructor() { }

    public version = '';
    public DeviceSystem = '';

    public getAPIName(): string {
        return 'CheckVersion';
    }

    public getRequestData(): APIRequest {
        let queryParams = new HttpParams();
        queryParams = queryParams.set("version", this.version);
        queryParams = queryParams.set("DeviceSystem", this.DeviceSystem);
        let requestData = new APIRequest();
        requestData.type = "GET";
        requestData.params = queryParams;
        return requestData;
    }

    public getMockPath(): string {
        return './assets/mock/CheckVersionAPI.json';
    }

}