class SelectOption{
    
  private _name: string;
  private _value: string;
  constructor(value : string, name : string) { 
    this._value = value;
    this._name = name;
  }

  public getName(): string {
      return this._name;
  }

  public getValue(): string {
      return this._value;
  }
}