import { MetaGrid } from './MetaGrid';
export class MetaColumn {
    constructor(id: string, name: string, type: string) {
        this.id = id;
        this.name = name;
        this.type = type;
    }
    public id: string = null;
    public name: string = null;
    public type: string = null;
    public optionRef: string = null;
    public showTitle: boolean = null;
    public required: boolean = null;
    public readonly: boolean = null;
    public inline: boolean = null;
    public grid: MetaGrid = null;
    public order: number = null;
    public default: any = null;
    public maxLength: number = null;
    public placeholder: string = null;
    //For Group
    public groupColumns: Array<MetaColumn> = [];
    public groupRows: Array<Array<MetaColumn>> = [];
    public icon: string = null;
    public canAdd: boolean = null;
    public maxNumber: number = null;
    public showDefaultOption: boolean = null;
    public rowTitle: string = null;
}