export class APIRequest {
    constructor() {
        this._body = {};
        this._type = "GET";
        this._url = '';
        this._params = null;
        this._timeout = 10000;
    }
    private _body;
    private _type;
    private _url;
    private _params;
    private _timeout;
    public get params() {
        return this._params;
    }
    public set params(value) {
        this._params = value;
    }
    public get type() {
        return this._type;
    }
    public set type(value) {
        this._type = value;
    }
    public get url() {
        return this._url;
    }
    public set url(value) {
        this._url = value;
    }
    public get body() {
        return this._body;
    }
    public set body(value) {
        this._body = value;
    }
    public get timeout() {
        return this._timeout;
    }
    public set timeout(value) {
        this._timeout = value;
    }
}