import { IDeviceDao } from "./DeviceDao.interface";
import { Observable } from "rxjs";
import { Address } from "./contact/Address";
import { Phone } from "./contact/Phone";
import { ContactItem } from "./contact/ContactItem";
declare var ContactFindOptions;
declare var window;

export class CordovaDeviceDao implements IDeviceDao {
    constructor() { }

    public searchcontactsByName(name: string): Observable<any> {
        var _navigator = (<any>navigator);
        var fields = [_navigator.contacts.fieldType.displayName, _navigator.contacts.fieldType.name];
        var options = new ContactFindOptions();
        options.filter = name;
        options.multiple = true;
        options.desiredFields = [_navigator.contacts.fieldType.name, _navigator.contacts.fieldType.emails, _navigator.contacts.fieldType.addresses, _navigator.contacts.fieldType.phoneNumbers];
        return Observable.create((observer) => {
            _navigator.contacts.find(fields, (contacts) => {
                console.log('find contacts results:', contacts);
                var returnObj = {
                    "Body": []
                }
                // var results = [];
                for (let contact of contacts) {
                    let firstName = contact.name.givenName || '';
                    let lastName = contact.name.familyName || '';
                    let birthday = contact.name.birthday || null;
                    let emails = contact.emails == null ? [] : contact.emails.map(x => x.value);
                    let phones = contact.phoneNumbers == null ? [] : contact.phoneNumbers.map(x => new Phone(x.type, x.value.trim()));
                    let address = contact.addresses == null ? [] : contact.addresses.map(this.convertAddress);

                    returnObj['Body'].push(new ContactItem(firstName, lastName, birthday, phones, emails, address));
                }
                observer.next(returnObj);
                observer.complete();
            }, (contactError) => {
                console.log('contacts error:', contactError, options);
                observer.error(contactError);
                observer.complete();
            }, options);
        })
    }

    private convertAddress(item): Address {
        let type = item.type || null;
        let address = item.streetAddress || null;
        let area = item.locality || null;
        let city = item.reigon || null;
        let code = item.postalCode || null;
        return new Address(type, city, area, code, address);
    }

    public async registerNotfiy(): Promise<any> {
        this.checkNotifyPermission();
        let token = await this.getFirbaseToken();
        while (token === null) {
            await this.waitnseconds(2000);
            token = await this.getFirbaseToken();
        }
        return token;
    }

    public onNotifyTokenRefresh(callback): void {
        return window.FirebasePlugin.onTokenRefresh((token) => {
            callback(token);
        }, (err) => {
            console.warn('token refresh error:', err.message);
        });;
    }

    public onNotificationOpen(callback): void {
        window.FirebasePlugin.onMessageReceived((notification) => {
            callback(notification);
        }, (err) => {
            console.warn('notification open error:', err.message);
        });
    }

    public subscribeSubject(subject: string): void {
        window.FirebasePlugin.subscribe(subject);
    }

    public unsubscribeSubject(subject: string): void {
        window.FirebasePlugin.unsubscribe(subject);
    }

    private checkNotifyPermission(): void {
        window.FirebasePlugin.hasPermission(function (data) {
            if (!data.isEnabled) {
                window.FirebasePlugin.grantPermission();
            }
        });
    }

    private getFirbaseToken(): Promise<any> {
        return new Promise((res, rej) => {
            window.FirebasePlugin.getToken((token) => {
                console.log("firebase gettoken:", token);
                res(token);
            }, (err) => {
                console.warn('get token error:', err.message);
                rej(err);
            });
        })
    }

    private waitnseconds(second): Promise<any> {
        return new Promise((res, rej) => {
            setTimeout(() => {
                res();
            }, second);
        })
    }
}