export class GoalExtension{

    private _Key: string;
    private _Value: string;


    constructor(Key, Value){
        this._Key = Key;
        this._Value = Value;
    }

    public get Value(): string {
        return this._Value;
    }
    public set Value(value: string) {
        this._Value = value;
    }
    public get Key(): string {
        return this._Key;
    }
    public set Key(value: string) {
        this._Key = value;
    }
}