import { IRestriction } from '../Restriction.interface';
export class LimitRestriction implements IRestriction {
    private values: Array<string>;
    constructor(values = []) {
        this.values = values;
    }
    sqlString(): string {
        return `LIMIT ${this.values[0].toString()} `;
    }

    getValues() {
        return this.values;
    }
}