import { ValidBean } from './ValidBean';
import { ValidCondition } from './condition/ValidCondition.interface';

export class ValidController {
    public static beanPool: Array<ValidBean> = [];

    public static addBean(bean: ValidBean): void {
        if (this.beanPool.filter(x => x.name === bean.name).length === 0)
            this.beanPool.push(bean);
    }

    public static validObject(data: any, name: string): boolean {
        try {
            console.log("validObject:", name, data);
            let bean = this.getBean(name);
            if (bean) {
                let validResult = bean.validSelf(data);
                if (validResult.length > 0) {
                    this.errorHandler(validResult, name);
                }
                return validResult.length === 0;
            }
            else {
                throw new Error(`Cannot find Bean ${name}.`);
            }
        } catch (error) {
            console.error('Valid Object error', error);
            return true;
        }
    }

    public static addCondition(objectName: string, propertyName: string, condition: ValidCondition): void {
        let bean = this.getBean(objectName);
        let property = bean.getOrCreateProperty(propertyName);
        console.log("addCondition:", bean, property);
        property.addCondition(condition);
    }

    private static getBean(name: string): ValidBean {
        if (this.beanPool.filter(x => x.name === name).length > 0)
            return this.beanPool.filter(x => x.name === name)[0]
        else
            return null;
    }

    private static errorHandler(validResult: Array<string>, name: string) {
        console.error(validResult.reduce((x, y) => `${x}\n${y}`, `Class ${name} valid error:`));
    }
}





