import { IAPI } from '../API.interface';
import { IRestfulAPI } from '../RestfulAPI.interface';
import { APIRequest } from '../APIRequest';
import { HttpParams } from '@angular/common/http';
import { IMockAPI } from '../MockAPI.interface';


export class PushActionLogAPI implements IAPI, IMockAPI, IRestfulAPI {

    public url: string = '';
    public actionList: Array<any> = [];

    constructor() { }

    getAPIName() {
        return 'PushActionLog';
    }

    getRequestData() {
        let requestData = new APIRequest();
        requestData.url = this.url;
        requestData.body = this.actionList.map(action => {
            return {
                "ActionID": action.ActionID,
                "FunctionID": action.Function,
                "Message": action.Message,
                "Time": new Date(action.Time).toISOString(),
                "Action": action.Action,
                "Valid": action.Valid,
                "DeviceModel": action.DeviceModel,
                "DeviceSystem": action.DeviceSystem
            }
        })
        requestData.type = "POST";
        return requestData;
    }

    getMockPath() {
        return './assets/mock/logout.json';
    }
}