import { StringUtils } from "./StringUtils";

export class NumberUtils {
    constructor() {
    }

    public static isNumber(val) {
        if(StringUtils.isNotEmpty(val)){
            return !isNaN(Number(val));
        }else{
            return false;
        }
    }

    public static isDecimal(val) {
        let result = false;
        if (this.isNumber(val)) {
            if (val.toString().indexOf('.') != -1) {
                result = true;
            }
        }
        return result;
    }

    public static isPositive(val) {
        let result = false;
        console.log('this.isNumber(val)', this.isNumber(val));
        if (this.isNumber(val)) {
            if (Number(val) > 0 || Number(val) == 0) {
                console.log('val', val);
                result = true;
            }
        }
        return result;
    }

    public static isPositiveInt(val) {
        let result = false;
        if (!this.isDecimal(val)) {
            if (Number(val) >= 0) {
                result = true;
            }
        }
        return result;
    }

    static numberToFix(n: number, toFix: number): number {
        let fix10 = Math.pow(10, toFix);
        return Math.round(this.strip(n) * fix10) / fix10;
    }

    static strip(num, precision = 12) {
        return Number(num.toPrecision(precision));
    }

}