import { Observable } from 'rxjs';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { DaoFactory } from '../../device/sqlite/DaoFactory';
import { SQLiteTable } from '../../device/sqlite/SQLiteTable';
import { GreaterOrEqualRestriction } from '../../device/sqlite/restrictions/GreaterOrEqualRestriction';

export class ProfileCodeAPI implements IAPI, IMockAPI, ISQLiteAPI {

  private _DaoFactory;
  constructor(DaoFactory: DaoFactory) {
    this._DaoFactory = DaoFactory;
  }

  getAPIName(): string {
    return 'getProfileCode';
  }

  getMockPath(): string {
    return './assets/mock/getCode.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let profileObj = this._DaoFactory.getTable('Profile', 'TW_LH_SD_Code');
      let dao = this._DaoFactory.getDao('Profile');
      if (profileObj != undefined) {
        (<SQLiteTable>profileObj).addRestriction(new GreaterOrEqualRestriction("ValidityPeriod", [Date.now()]));
        dao.queryByTable(profileObj).subscribe((resp) => {
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }

}
