import { Injectable, Inject, NgZone, Optional } from '@angular/core';
import { IAuthRoute } from '../AuthRoute.interface';
import { IAuthAction } from '../AuthAction.interface';
import { AuthObject, AuthError } from '../AuthObject';
import { checkTimeoutToken, ConfigToken, timeoutActionToken } from '../../injectionToken';
import { checkTimeout } from './checkTimeout.interface';
import { timeoutAction } from './timeoutAction.interface';
import { Subject, BehaviorSubject, Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class TimeoutService implements IAuthRoute, IAuthAction {

  private seconds: number = 0;
  private reset_seconds: number;
  private reaminingSubject: Subject<number> = new BehaviorSubject(this.seconds);
  private _interval = null;

  constructor(
    @Inject(ConfigToken) private APP_CONFIG: any,
    @Optional() @Inject(checkTimeoutToken) private timeoutCheck: checkTimeout,
    @Optional() @Inject(timeoutActionToken) private timeoutAction: timeoutAction,
    private zone: NgZone) {
    this.reset_seconds = this.APP_CONFIG[this.APP_CONFIG.ENV].TIMEOUT;
    this.seconds = this.reset_seconds;
  }

  reset() {
    if (this.timeoutCheck)
      this.timeoutCheck.reset();
    else {
      this.seconds = this.reset_seconds;
    }
  }

  init() {
    if (this.timeoutCheck) {
      this.timeoutCheck.init();
    }
    else {
      if (this._interval != null) {
        clearInterval(this._interval);
      }
      this.zone.runOutsideAngular(() => {
        this._interval = setInterval(() => {
          this.seconds--;
          this.reaminingSubject.next(this.seconds);
          if (this.seconds <= 0) {
            this.zone.run(() => {
              clearInterval(this._interval);
              this._interval = null;
              console.log('timeout!!');
              if (this.timeoutAction) {
                this.timeoutAction.timeoutAction();
              }
            })
          }
        }, 1000);
      });
    }

  }

  clear() {
    if (this.timeoutCheck)
      this.timeoutCheck.clear();
    else {
      if (this._interval != null) {
        clearInterval(this._interval);
        this._interval = null;
      }
    }
  }

  authRoute(payload: AuthObject) {
    if (!payload.status)
      return payload;
    payload.status = !this.isTimeout();
    if (!payload.status) {
      payload.error = AuthError.TIMEOUT;
    }
    return payload;
  }
  authAction(payload: AuthObject) {
    if (!payload.status)
      return payload;
    payload.status = !this.isTimeout();
    if (!payload.status) {
      payload.error = AuthError.TIMEOUT;
    }
    return payload;
  }

  getTimeoutRemainingSecond(): Observable<number> {
    return this.reaminingSubject.asObservable();
  }
  private isTimeout() {
    if (this.timeoutCheck)
      return this.timeoutCheck.checkTimeout();
    else
      return this.seconds <= 0;
  }
}
