import { IAPI } from "../API.interface";
import { IMockAPI } from "../MockAPI.interface";
import { IDeviceAPI } from "../DeviceAPI.interface";
import { Observable } from "rxjs";
import { DeviceFactory } from "../../device/DeviceFactory";


export class ContactsSearchAPI implements IAPI, IMockAPI, IDeviceAPI {
   
    constructor(private deviceFactory: DeviceFactory){
        
    }

    getAPIName(): string {
        return 'contactsSearch';
    }

    getMockPath(): string {
        return './assets/mock/getImportContact.json';
    }

    runDeviceAPI(): Observable<any> {
        let dao = this.deviceFactory.getDefaultDao();
        return dao.searchcontactsByName('');
    }

}