import { ValidController } from "../ValidController";
import { Observable, isObservable, of } from "rxjs";

export function Valid(type?: any): MethodDecorator {
    console.log("valid type:", type);
    return function (
        target: Function,
        propertyName: string,
        propertyDesciptor: PropertyDescriptor) {
        const method = propertyDesciptor.value;
        propertyDesciptor.value = function (...args) {
            console.log('pool:', ValidController.beanPool);
            let result = method.apply(this, args);
            console.log('valid result: ', result);
            if (isObservable(result)) {
                console.log('isObservable(result)');
                return handleObservable(result);
            }
            else if (result instanceof Promise) {
                console.log('result instanceof Promise');
                return handlePromise(result);
            }
            else {
                console.log('not Observable, not instanceof Promise');
                validObject(result);
                return result;
            }
        }
        return propertyDesciptor;
    }
}

function handleObservable(result: Observable<any>): Observable<any> {
    return new Observable((observer) => {
        result.subscribe(
            {
                next(x) {
                    validObject(x);
                    observer.next(x);
                },
                error(err) { observer.error(err); },
                complete() { observer.complete(); }
            })
    });
}

async function handlePromise(result: Promise<any>): Promise<any> {
    let resolved = await result;
    validObject(resolved);
    return resolved;
}

function validObject(data: any) {
    console.log("Valid.ts validObject:", data);
    if (data instanceof Array)
        data.forEach(x => validObject(x));
    else
        ValidController.validObject(data, data.beanName);
}