import { SQLiteTable } from "../device/sqlite/SQLiteTable";

export class TableUtils {
    static fillTableColumn(tableObject: SQLiteTable, data: any): SQLiteTable {
        let columns = tableObject.getColumns().map(x => x.getName()).filter(x => x !== 'ClientID');
        if (data != undefined) {
            if (columns.length > 0) {
                for (let col of columns) {
                    if (data[col] != undefined)
                        tableObject.setValue(col, data[col]);
                }
            }
        }
        return tableObject;
    }
}

