export class RouteUtils {

    public static convertJsonToRouterMap(data: any): any {
        let map = {};
        let funcList = Object.keys(data);
        funcList.forEach(func_name => {
            Object.keys(data[func_name]).forEach(pageName => {
                data[func_name][pageName] = Object.assign(data[func_name][pageName], {
                    Function: func_name
                })
                map[pageName] = data[func_name][pageName];
            })
        })
        return map;
    }
}