import { Injectable, Inject } from '@angular/core';
import { IAuthAction } from '../AuthAction.interface';
import { AuthObject, AuthError } from '../AuthObject';
import { Subject, BehaviorSubject, Observable } from 'rxjs';
import { ConfigToken } from '../../injectionToken/injection-token';

export enum ACTION_STATUS {
  AVAILABLE,
  PENDING
}

export class ActionEvent {
  public action: string;
  public event: any;
}

@Injectable({
  providedIn: 'root'
})
export class ActionService implements IAuthAction {

  constructor(
    @Inject(ConfigToken) private APP_CONFIG: any
  ) {
    this.actionIntervalSec = this.APP_CONFIG.ACTION_INTERVAL;
  }

  private currentAction = null;
  private actionIntervalSec = 800;
  private status = ACTION_STATUS.AVAILABLE;
  private statusSubject: Subject<ACTION_STATUS> = new BehaviorSubject(this.status);
  private actionSuscribe: Subject<ActionEvent> = new Subject();

  authAction(payload: AuthObject): AuthObject {
    if (this.status === ACTION_STATUS.AVAILABLE) {
      this.status = ACTION_STATUS.PENDING;
      this.statusSubject.next(this.status);
      //Prevent fast click
      setTimeout(() => {
        this.status = ACTION_STATUS.AVAILABLE;
        this.statusSubject.next(this.status);
      }, this.actionIntervalSec);
    }
    else {
      //Other Action is active
      payload.status = false;
      payload.error = AuthError.PENDING_ACTION;
    }
    return payload;
  }

  getCurrentAction(): string {
    return this.currentAction;
  }

  getActionStatus(): Observable<ACTION_STATUS> {
    return this.statusSubject.asObservable();
  }

  onActionClick(): Observable<ActionEvent> {
    return this.actionSuscribe.asObservable();
  }

  actionClick(event: ActionEvent) {
    this.actionSuscribe.next(event);
  }


}
