export class SQLiteResponse {
    private _header = {};
    private _body = [];
    constructor(info, data) {
        this._header = info;
        this._body = data;
    }

    get Header(){
      return this._header;
    }

    set Header(val:any){
      this._header = val;
    }

    get Body(){
      return this._body;
    }

    set Body(val:any){
      this._body = val;
    }

}
