import { IAPI } from '../API.interface';
import { IRestfulAPI } from '../RestfulAPI.interface';
import { APIRequest } from '../APIRequest';
import { HttpParams } from '@angular/common/http';

export class syncSequenceIDAPI implements IAPI, IRestfulAPI {

    public num: number = 0;
    public type: string = '';

    constructor() { }

    getAPIName() {
        return 'getSyncSequenceID';
    }

    getRequestData() {
        let requestData = new APIRequest();
        let queryParams = new HttpParams();
        queryParams = queryParams.set("category", this.type);
        queryParams = queryParams.set("idNums", this.num.toString());
        requestData.params = queryParams;
        requestData.type = "GET";
        return requestData;
    }
}