import { Address } from "./Address";
import { Phone } from "./Phone";

export class ContactItem {
    private _firstname: string;
    private _lastname: string;
    private _phones: Array<Phone>;
    private _emails: Array<string>;
    private _address: Array<Address>;
    private _birthday : Date;
    
    public _isCheck : boolean = false;
    public _isShow : boolean = false;
    constructor(firstname, lastname,birthday, phones, emails, address) {
        this._firstname = firstname;
        this._lastname = lastname;
        this._birthday = birthday;
        this._phones = phones;
        this._emails = emails;
        this._address = address;
        this._isShow = true;
    }

    public set Birthday(birthday:Date) {
        this._birthday = birthday;
    }

    public get Birthday() {
        return this._birthday;
    }

    public set isCheck(check : boolean) {
        this._isCheck = check;
    }

    public get isCheck() {
        return this._isCheck;
    }

    public set isShow(show : boolean) {
        this._isShow = show;
    }
    public get isShow() {
        return this._isShow;
    }    
    public get FirstName(): string {
        return this._firstname;
    }
    public set FirstName(value: string) {
        this._firstname = value;
    }
    public get LastName(): string {
        return this._lastname;
    }
    public set LastName(value: string) {
        this._lastname = value;
    }
    public get tel(): Array<Phone> {
        return this._phones;
    }
    public set tel(value: Array<Phone>) {
        this._phones = value;
    }
    public get email(): Array<string> {
        return this._emails;
    }
    public set email(value: Array<string>) {
        this._emails = value;
    }
    public get address(): Array<Address> {
        return this._address;
    }
    public set address(value: Array<Address>) {
        this._address = value;
    }

    public get fullname(): string {
        return this.FirstName + ' ' + this.LastName;
    }

}