import { ValidProperty } from "./ValidProperty";
import * as _ from 'lodash';

export class ValidBean {
    private _name: string;
    private _type: any;
    private propertyList: Array<ValidProperty>;

    public get name(): string {
        return this._name;
    }
    public set name(value: string) {
        this._name = value;
    }

    public get type(): any {
        return this._type;
    }
    public set type(value: any) {
        this._type = value;
    }

    constructor(name: string, type: any) {
        this.name = name;
        this.type = type;
        this.propertyList = [];
    }

    public getOrCreateProperty(name: string): ValidProperty {
        let filtered = this.propertyList.filter(x => x.name === name);
        if (filtered.length > 0) {
            return filtered[0];
        } else {
            let property = new ValidProperty(name);
            this.addProperty(property);
            return property;
        }
    }

    public validSelf(data: any): Array<string> {
        let validResult = _.flatMap(this.propertyList, x => x.validProperty(data[x.name]));
        console.log("validSelf result:", validResult);
        return validResult;
    }

    private addProperty(property: ValidProperty) {
        this.propertyList.push(property);
    }

}