export class LanguageCode{
  private _languageID : string;
  private _name : string;
  private _isDefault : string;

  constructor(id,name,isDefault) {
    this._languageID = id;
    this._name = name;
    this._isDefault = isDefault;
  }

  get getLanguageID() {
    return this._languageID;
  }

  get getName() {
    return this._name;
  }

  get getIsDefault() {
    return this._isDefault;
  }
}
