import { Injectable, ErrorHandler, Injector } from "@angular/core";
import { Observable, Subject, BehaviorSubject } from "rxjs";
import { NotificationObject } from "./NotificationObject";
import { APIFactory } from "../api/APIFactory";
import { APIDispatch } from "../api/APIDispatch";
import { DashboardGetMessageListAPI } from "../api/register/DashboardGetMessageListAPI";
import { APPError } from "../errorHandler/APPError";
import { DashboardUpdateMessageStatusAPI } from "../api/register/DashboardUpdateMessageStatusAPI";
import { NotificationProvider } from "./NotificationProvider";
import { NotificationType } from "./INotificationProvider.interface";
import * as _ from 'lodash';

@Injectable({
    providedIn: 'root'
})
export class NotificationMgr {

    private DBMessage: Array<NotificationObject> = [];
    private notificationList: Array<NotificationObject> = [];
    private notificationSubject: Subject<Array<NotificationObject>> = new BehaviorSubject(this.notificationList);
    constructor(
        private injector: Injector,
        private errorHandler: ErrorHandler,
        private notificationProvider: NotificationProvider
    ) {

    }

    async init() {
        console.log("NotificationMgr init");
        await this.fetchMessageData();
    }

    public getNotificationList(): Observable<Array<NotificationObject>> {
        console.log("NotificationMgr getNotificationList");
        return this.notificationSubject.asObservable();
    }

    public pushNotification(type: NotificationType, data: any) {
        let notify = this.notificationProvider.getNotificationObject(type, data);
        console.log("NotificationMgr pushNotification:", notify);
        // reminder message
        if (notify.action) {
            let duplicateList = this.notificationList.filter(x => x.category === notify.category && x.type === notify.type);
            let conflictResult = notify.action.onConflict(duplicateList, notify);
            this.notificationList = [...conflictResult, ...this.notificationList.filter(x => x.category !== notify.category || x.type !== notify.type)]
        }
        else {
            this.notificationList.push(notify);
        }


        this.notificationList = this.notificationList.sort((a, b) => a.order - b.order);
        this.notificationSubject.next(this.notificationList);
    }

    public popNotification(notification: NotificationObject) {
        console.log("NotificationMgr popNotification:", notification);
        try {
            let popped = this.notificationList.filter(x => x.id === notification.id).length > 0;
            console.log("popNotification:", popped);
            if (popped) {
                this.notificationList = this.notificationList.filter(x => x.id !== notification.id);
                this.notificationSubject.next(this.notificationList);
                console.log("after popped:", this.notificationList);
            }
        } catch (error) {
            this.errorHandler.handleError(new APPError("F00021", error.message));
        }
    }

    public getUnreadMessageNumber() {
        return this.DBMessage.length;
    }

    public popAllNotification() {
        this.notificationList = [];
        this.notificationSubject.next(this.notificationList);
    }

    public showCategoryMessage(category: string = null, type: string = null) {
        console.log("NotificationMgr ShowCategoryMessage:", category, type);
        let selected = this.DBMessage.filter(x =>
            ((x.category === category) || (!category)) && ((x.type === type) || (!type)))
        let selected_id = selected.map(x => x.id);
        if (selected.length > 0) {
            this.notificationList = [...this.notificationList, ...selected];
            this.notificationSubject.next(this.notificationList);
            this.DBMessage = this.DBMessage.filter(x => selected_id.indexOf(x.id) < 0);

            // update db message status
            let APIFactory = this.getAPIFactory();
            let dispatcher = this.getAPIDispatch();
            let updateMessageStatusAPI = APIFactory.getAPI('updateDashboardMessageStatus');
            (<DashboardUpdateMessageStatusAPI>updateMessageStatusAPI).setClientIDList(selected_id);
            (<DashboardUpdateMessageStatusAPI>updateMessageStatusAPI).setStatus('Reading');
            dispatcher.dispatch(updateMessageStatusAPI).subscribe((resp) => {
                console.log("NotificationMgr popNotification updateMessageStatusAPI resp:", resp);
            })
        }
    }

    public async fetchMessageData() {
        console.log("NotificationMgr fetchMessageData");
        let fetched_messages = [];
        try {
            let APIFactory = this.getAPIFactory();
            let dispatcher = this.getAPIDispatch();
            let getMessageAPI = APIFactory.getAPI("getDashboardMessageList");
            (<DashboardGetMessageListAPI>getMessageAPI).setKeyword("UnRead");
            (<DashboardGetMessageListAPI>getMessageAPI).setIsPopup(true);
            let resp = await dispatcher.dispatch(getMessageAPI).toPromise();
            let messageList = resp['Body'];
            fetched_messages = messageList.map(x => {
                return this.notificationProvider.getNotificationObject(x.MessageType, Object.assign({
                    "Source": "DB",
                    "Title": x.Title,
                    "Description": x.Description,
                }, JSON.parse(x.Arguments)), x.ClientID);
            });
        }
        catch (err) {
            this.errorHandler.handleError(new APPError("F00020", err.message));
        }
        this.DBMessage = fetched_messages;
        console.log("NotificationMgr fetchMessageData end", this.DBMessage);
    }

    private getAPIFactory(): APIFactory {
        try {
            let factory = this.injector.get(APIFactory);
            return factory;
        }
        catch{
            return null;
        }
    }

    private getAPIDispatch(): APIDispatch {
        try {
            let dispatcher = this.injector.get(APIDispatch);
            return dispatcher;
        } catch  {
            return null;
        }
    }
}