import { Observable, of, from } from 'rxjs';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { DaoFactory } from '../../device/sqlite/DaoFactory';
import { TranslateService } from '../../language/translate.service';
import { DeviceService } from '../../device/device.service';
import { EqualRestriction } from '../../device';

export class ChangeMessageStatusAPI implements IAPI, IMockAPI, ISQLiteAPI {

    private _messageID = null;
    private _col = null;
    private _val = null;

    public get val() {
        return this._val;
    }
    public set val(value) {
        this._val = value;
    }

    public get col() {
        return this._col;
    }
    public set col(value) {
        this._col = value;
    }
    public get messageID() {
        return this._messageID;
    }
    public set messageID(value) {
        this._messageID = value;
    }

    constructor(private daoFactory: DaoFactory) {
    }

    getAPIName(): string {
        return 'ChangeMessageStatus';
    }

    getMockPath(): string {
        return './assets/mock/addCalendarEvent.json';
    }

    executeSQL(): Observable<any> {

        if(this._messageID && this._col && this._val){
            return from(this._updateStatus(this._messageID, this._col, this._val));
        }else{
            return of(false);
        }
    }

    private async _updateStatus(messageID, col, val){
        let resp = null;
        let dao = this.daoFactory.getDefaultDao();
        let Message = this.daoFactory.getDefaultTable('TW_LH_SD_Message');
        console.log("_updateStatus Message",Message)
        if(Message){
            Message.addRestriction(new EqualRestriction("MessageID",[messageID]));
            let messageResp = await dao.queryByTable(Message).toPromise();
            if(messageResp.Header["status"] && messageResp.Body.length>0){
                Message.setValue(col,val);
                resp = await dao.updateByTable(Message).toPromise();
            }
        }
        console.log("_updateStatus resp", resp);
        return resp;
    }

}
