import { IAPI } from '../API.interface';
import { IRestfulAPI } from '../RestfulAPI.interface';
import { APIRequest } from '../APIRequest';
import { HttpParams } from '@angular/common/http';
import { IMockAPI } from '../MockAPI.interface';
import { Inject, Optional } from '@angular/core';
import { ConfigToken } from '../../injectionToken/injection-token';

export class getGoalAPI implements IAPI, IRestfulAPI, IMockAPI{

    constructor(
        private APP_CONFIG: any
    ) { }


    private agentID = '';
    private url = '';
    
    public lastUpdateTime: string = '2019-01-01 00:00:00';

    public setAgentID( agent_id){
        this.agentID = agent_id;
    }

    getMockPath(): string {
        return 'assets/mock/Goal.json';
    }

    getAPIName() {
        return 'getGoal';
    }

    getRequestData() {
        let requestData = new APIRequest();
        let env = this.APP_CONFIG.ENV;
          if(this.agentID != '' && this.agentID != undefined && this.agentID != null){
            this.url = this.APP_CONFIG[env].API_URL.getGoal;
            requestData.url = this.url + '/' + this.agentID;
        }else{
            requestData.url = this.url;
        }
        requestData.type = "GET";
        requestData.params = new HttpParams()
        .set('lastUpdateTime', this.lastUpdateTime);
        return requestData;
    }
}