import { IAPI } from '../API.interface';
import { IRestfulAPI } from '../RestfulAPI.interface';
import { APIRequest } from '../APIRequest';
import { HttpParams } from '@angular/common/http';
import { IMockAPI } from '../MockAPI.interface';


export class PushErrorLogAPI implements IAPI, IMockAPI, IRestfulAPI {

    public url: string = '';
    public errorList: Array<any> = [];

    constructor() { }

    getAPIName() {
        return 'PushErrorLog';
    }

    getRequestData() {
        let requestData = new APIRequest();
        requestData.url = this.url;
        requestData.body = this.errorList.map(err => {
            return {
                "Message": err.Message,
                "Stack": err.Stack,
                "Time": new Date(err.Time).toISOString(),
                "DeviceModel": err.DeviceModel,
                "DeviceSystem": err.DeviceSystem
            }
        })
        requestData.type = "POST";
        return requestData;
    }

    getMockPath() {
        return './assets/mock/logout.json';
    }
}