import { Injectable, Optional, Inject } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { LoadingApp } from './LoadingApp.interface';
import { VersionCheckService } from '../../dataSync/version-check.service';
import { DataSyncService } from '../../dataSync/data-sync.service';
import { LogService } from '../../service/log/log.service';
import { ProfileCodeService } from '../../service/profile/profile-code.service';
import { NotificationMgr } from '../../notification/NotificationMgr';
import { NotificationType } from '../../notification/INotificationProvider.interface';
import { PushIDMgrToken } from '../../injectionToken/injection-token';
import { PushIDMgr } from './PushIDMgr.interface';
import { SettingService } from '../../service/setting/setting.service';


@Injectable({
    providedIn: 'root'
})
export class DefaultLoadingApp implements LoadingApp {
    constructor(
        private versionCheck: VersionCheckService,
        private syncService: DataSyncService,
        private logService: LogService,
        private profileCodeService: ProfileCodeService,
        private notificationMgr: NotificationMgr,
        private settingService: SettingService,
        @Optional() @Inject(PushIDMgrToken) private pushIDMgr: PushIDMgr
    ) {
        this.loadedSubject = new Subject<number>();
    }
    private loadedSubject: Subject<number>;
    loading(): void {
        this._loading();
    }

    onLoaded(): Observable<number> {
        return this.loadedSubject.asObservable();
    }

    private async _loading() {
        await this.syncService.syncAllFunc(true);
        this.loadedSubject.next(50);
        await this.profileCodeService._fetchCodeData().toPromise();
        this.loadedSubject.next(60);
        await this.logService.pushActionLog().toPromise();
        this.loadedSubject.next(70);
        await this.logService.pushErrorLog().toPromise();
        this.loadedSubject.next(80);
        await this.notificationMgr.init();
        this.loadedSubject.next(85);
        await this.settingService.deviceChange(this.pushIDMgr.getPushID());
        this.loadedSubject.next(88);
        //after sync finish ,check version
        let versionResp = await this.versionCheck.checkVersion().toPromise();
        if (versionResp.isSuccess) {
            this.loadedSubject.next(95);
            if (versionResp.newVersion) {
                this.notificationMgr.pushNotification(NotificationType.NewVersionLogin, versionResp.appVersion);
            }
            else {
                setTimeout(() => {
                    this.loadedSubject.next(100);
                }, 800);
            }
        }
        else {
            console.log("Check version error!!");
            this.loadedSubject.next(100);
        }
    }





}