import { Directive, HostListener, Input, Output, EventEmitter, HostBinding, OnInit, ElementRef, AfterViewInit, Optional, Inject } from '@angular/core';
import { AuthService } from '../auth.service';
import { APIDispatch } from '../../api/APIDispatch';
import { APIFactory } from '../../api/APIFactory';
import { logActionAPI } from '../../api/register/logActionAPI';
import { DeviceService } from '../../device/device.service';
import { AppRouter } from '../../route/AppRouter';
import { ActionService, ACTION_STATUS, ActionEvent } from './action.service';
import { actionDirectiveTaskToken } from '../../injectionToken/injection-token';
import { IActionDirectiveTask } from '../../interface/ActionDirectiveTask.interface';


@Directive({
  selector: '[Action]'
})
export class ActionDirective implements AfterViewInit {

  constructor(
    private authService: AuthService,
    private APIFactory: APIFactory,
    private dispatcher: APIDispatch,
    private deviceService: DeviceService,
    private actionService: ActionService,
    private appRouter: AppRouter,
    private elRef: ElementRef,
    @Optional() @Inject(actionDirectiveTaskToken) private actionDirectiveTask: IActionDirectiveTask
  ) {
    this.actionService.getActionStatus().subscribe((status: ACTION_STATUS) => {
      this.isDisabled = status === ACTION_STATUS.PENDING;
    })


  }

  // start of highlight digital track
  ngAfterViewInit() {

    // this.settingService.getDebugMode().subscribe((debugMode: boolean) => {

    //   let elementTagName = this.elRef.nativeElement.localName;
    //   let targetElement = null;
    //   if (elementTagName === 'app-ui-btn') {
    //     targetElement = this.elRef.nativeElement.children[0];
    //     console.log('app-ui-btn targetElement: ', targetElement);
    //   }
    //   else if (elementTagName === 'app-ui-link') {
    //     targetElement = this.elRef.nativeElement.children[0].children[0];
    //     console.log('app-ui-link targetElement: ', targetElement);
    //   }
    //   else {
    //     targetElement = this.elRef.nativeElement;
    //   }


    //   if (targetElement) {
    //     if (debugMode) {
    //       targetElement.setAttribute('style', "border: 3px solid #ff0000 !important");
    //     }
    //     else {
    //       targetElement.setAttribute('style', "");
    //     }
    //   }

    // })


  }
  // @HostListener('mouseenter') onMouseenter(e) {
  //   this.elRef.nativeElement.innerText = this.action;
  // }

  //end of highlight digital track

  @Input() action;
  @Output() actionClick: EventEmitter<any> = new EventEmitter();
  @HostBinding('class.disabled') isDisabled = false;
  // @HostBinding('class.hightlightDigitalTrack') isHightight = true;
  @HostListener('onClick', ['$event'])
  @HostListener('onClickLink', ['$event'])
  @HostListener('onTabChildClick', ['$event'])
  @HostListener('ClickBtn', ['$event'])
  @HostListener('click', ['$event'])
  async onclick(e) {
    console.log('action click:', this.action, e);
    if (e.stopPropagation) { e.stopPropagation(); }
    if (e.preventDefault) { e.preventDefault(); }
    console.log("actionClick after output: ", e);
    let event = new ActionEvent();
    event.action = this.action;
    event.event = e;
    this.actionService.actionClick(event);
    let isAuth = await this.authService.authAction({ action: this.action });
    if (isAuth.status) {
      this.actionClick.emit(e);
      console.log("actionClick output: ", e);
      if (this.actionDirectiveTask) {
        await this.actionDirectiveTask.doTask(this.action);
      }
    }
    else {
      console.log(`action: ${this.action} auth failed.`);
    }
    let ActionAPI = this.APIFactory.getAPI("LogAction");
    (<logActionAPI>ActionAPI).action = this.action;
    (<logActionAPI>ActionAPI).time = new Date();
    (<logActionAPI>ActionAPI).valid = isAuth.status;
    (<logActionAPI>ActionAPI).message = isAuth.error;
    (<logActionAPI>ActionAPI).Function = this.appRouter.getCurrentFunction();
    (<logActionAPI>ActionAPI).DeviceModel = this.deviceService.getDeviceManufacturer();
    (<logActionAPI>ActionAPI).DeviceSystem = this.deviceService.getDevicePlatform();
    let resp = await this.dispatcher.dispatch(ActionAPI).toPromise();
    console.log("log Action resp:", resp);
  }
}
