export class ViewDateChange {
    private _viewDate: Date;
    private _action: ChangeAction;

    constructor({ date, action }) {
        this._viewDate = date;
        this._action = action;
    }

    public get viewDate(): Date {
        return this._viewDate;
    }
    public set viewDate(value: Date) {
        this._viewDate = value;
    }

    public get action(): ChangeAction {
        return this._action;
    }
    public set action(value: ChangeAction) {
        this._action = value;
    }

}

export enum ChangeAction {
    CLICK,
    SWIPE
}