import { Observable } from 'rxjs';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { DaoFactory } from '../../device/sqlite/DaoFactory';
import { ClientCustomDao } from '../../device/sqlite/ClientCustomDao';
import { EqualRestriction } from '../../device/sqlite/restrictions/EqualRestriction';
import { SQLiteTable } from '../../device/sqlite/SQLiteTable';
import { InRestriction } from '../../device/sqlite/restrictions/InRestriction';


export class DashboardUpdateMessageStatusAPI implements IAPI, IMockAPI, ISQLiteAPI {

    private _clientID: string;
    private _clientIDList: Array<string> = [];
    private _status: string;

    constructor(private daoFactory: DaoFactory) {

    }

    setClientID(clientID: string) {
        this._clientID = clientID;
    }

    setClientIDList(list: Array<string>) {
        this._clientIDList = list;
    }

    setStatus(status: string) {
        this._status = status;
    }

    getAPIName(): string {
        return 'updateDashboardMessageStatus';
    }

    getMockPath(): string {
        return './assets/mock/saveSuccess.json';
    }

    executeSQL(): Observable<any> {

        return Observable.create((observer) => {
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {

                //save customer data
                let messageObj = this.daoFactory.getDefaultTable("TW_LH_SD_Message");
                if (messageObj) {
                    dao = new ClientCustomDao(dao);

                    messageObj = (<SQLiteTable>messageObj);


                    if (this._clientIDList.length > 0) {
                        messageObj.addRestriction(new InRestriction('ClientID', this._clientIDList));
                    } else {
                        messageObj.addRestriction(new EqualRestriction('ClientID', [this._clientID]));
                    }
                    messageObj.setValue("Status", this._status);

                    dao.updateByTable(messageObj).subscribe((resp) => {

                        observer.next(resp);
                        observer.complete();
                    });
                }
                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
