import { BaseMetaComponent } from './BaseMetaComponent';
import { MetaService } from '../Meta.service';
import { ValidationResult } from '../../utils/ValidationResult';
import { MetaValid } from '../parser/bean/MetaValid';
import { MetaColumn } from '../parser/bean/MetaColumn';
import { ProfileCodeService } from '../../service/profile/profile-code.service';
import { SelectOption } from '../../bean/SelectOption';
import { ProfileCode } from '../../bean/ProfileCode';
import { IMetaExecutor } from '../executor/MetaExecutor.interface';
import { IMetaParser } from '../parser/MetaParser.interface';
import * as _ from 'lodash';


export abstract class FormMetaComponent extends BaseMetaComponent {


    public validationResult: ValidationResult = new ValidationResult();

    constructor(
        metaService: MetaService,
        protected profileCodeService: ProfileCodeService,
        parser: IMetaParser,
        executor: IMetaExecutor
    ) {
        super(metaService, parser, executor);
    }

    public ngOnInit() {
        console.log("FormMetaComponent ngOninit!");
        super.ngOnInit();
    }


    protected abstract onValueChange(column: string, value: any, groupId: string, index: number): void;
    protected abstract onValidateAll(): boolean;
    protected abstract btnClick(type: string, id: string): void;

    public onChange(column: string, value: any, groupID: string = null, index: number = -1) {
        console.log("onChange:", column, value, groupID, index);
        let isGroup = (groupID && index >= 0);
        let columnID = isGroup ? column : (column + index.toString());
        let validResp = this.valid(column, value, isGroup);
        console.log("validResp:", validResp);

        if (validResp)
            this.validationResult.deleteError(columnID);
        else
            this.validationResult.setErrorMap(columnID, validResp.message);

        this.onValueChange(column, value, groupID, index);

    }

    private valid(column: string, value: any, isGroup: boolean = false) {
        console.log("valid:", column, value, isGroup);
        let resp = new MetaValid(column, true, "");
        let col: MetaColumn;
        if (isGroup) {
            let groupCol = this.metaConfig.Columns
                .filter(x => x.type === 'Group')
                .map(x => x.groupColumns);
            col = _.flatten(groupCol).filter(x => x.id === column)[0];
        }
        else
            col = this.metaConfig.Columns.filter(x => x.id === column)[0];
        console.log("valid col:", col);
        if (col.type === 'Input') {
            if (col.required && !value) {
                resp.isValid = false;
                resp.message = col.id + "_required";
            }
        }
        else if (col.type === 'Select') {
            if (col.required && (value === '' || !value)) {
                resp.isValid = false;
                resp.message = col.id + "_required";
            }
        }
        return resp;
    }

    private validAll() {
        //Column Result
        let validResult = this.metaConfig.Columns.filter(x => x.type !== "Group")
            .map((col: MetaColumn) => this.valid(col.id, this._data[col.id]))
            .filter(x => !x.isValid);

        this.metaConfig.Columns.filter(x => x.type === "Group")
            .forEach(group => {
                let groupId = group.id;
                this._data[groupId].forEach((ele, index) => {
                    validResult = validResult.concat(group.groupColumns.map(col => {
                        let validResp = this.valid(col.id, ele[col.id], true);
                        validResp.colID = col.id + index.toString();
                        return validResp;
                    }).filter(x => !x.isValid));
                });
            });
        console.log("validAll result:", validResult);
        return validResult;
    }

    public async onBtnClick(type: string, id: string) {
        if (this._data[id + 'Btn'])
            this._data[id + 'Btn'].disable = true;
        if (type === 'submit') {
            this.validationResult.clearErrorMap();
            let result = this.validAll();
            result.forEach((err) => {
                this.validationResult.setErrorMap(err.colID, err.message);
            })
            if (this.onValidateAll() && result.length === 0) {
                //CALL SAVE API
                let resp = await this.saveData();
                console.log("save data resp:", resp);
                this.btnClick(type, id);
                if (this._data[id + 'Btn'])
                    this._data[id + 'Btn'].disable = true;
            }
            else {
                this._data[id + 'Btn'].disable = false;
            }
        }
        else
            this.btnClick(type, id);
    }

    protected async saveData() {
        return await this.executor.saveData(this.metaConfig, this._data, this.getMetaParams());
    }

    public onGroupRemove(groupName: string, index: number) {
        this._data[groupName] = this._data[groupName].filter((x, i) => i != index);
        let group_col = this.metaConfig.Columns.filter(x => x.id === groupName)[0].groupColumns;
        group_col.forEach(col => {
            this.validationResult.deleteError(col.id + index.toString());
        });

    }

    public onGroupAdd(groupName: string) {
        let group_col = this.metaConfig.Columns.filter(x => x.id === groupName)[0].groupColumns;
        let keys = group_col.map(x => x.id);
        console.log("keys:", keys);
        let newObj = keys.map(x => {
            let y = {};
            let defaultVal = group_col.filter(z => z.id === x)[0].default;
            y[x] = defaultVal;
            return y
        }).reduce((acc, current) => {
            return Object.assign(acc, current);
        }, {})
        console.log("GroupAdd newObj:", newObj);
        this._data[groupName] = [...this._data[groupName], newObj];

    }

    protected onDataUpdated() {
        this.addBtnAttr();
        this.fetchOptions();
    }

    private fetchOptions() {
        this.metaConfig.Columns
            .filter(x => x.optionRef && x.optionRef.length > 0)
            .map(x => this.createOption(x))
            .forEach((option) => {
                this._data[option.key] = option.val;
            })

        let groupCol = this.metaConfig.Columns
            .filter(x => x.type === 'Group')
            .map(x => x.groupColumns.filter(y => y.optionRef && y.optionRef.length > 0));

        let groupOpt = _.flatMap(groupCol, x => x.map(y => this.createOption(y)));
        groupOpt.forEach((option) => {
            this._data[option.key] = option.val;
        })

    }

    private addBtnAttr() {
        this.metaConfig.Footer.forEach(ele => {
            if (ele.type === 'submit' || ele.type === 'button')
                this._data[ele.id + "Btn"] = Object.assign({
                    disable: false
                }, ele);
        });
    }

    private createOption(column: MetaColumn) {
        let resp = {
            key: column.id + 'Option',
            val: []
        }
        let codeArray = this.profileCodeService.getCodeArray(column.optionRef);
        if (column.type === 'Select')
            resp.val = codeArray
                .map((x: ProfileCode) => new SelectOption(x.getCode(), this.profileCodeService.convertCode2Text(column.optionRef, x.getCode())));
        else
            resp.val = codeArray.map((x: ProfileCode) => {
                return {
                    id: x.getCode(),
                    name: this.profileCodeService.convertCode2Text(column.optionRef, x.getCode()),
                    arguments: JSON.parse(x.getArguments())
                }
            })

        return resp;
    }

}