import { Injectable, Inject, Optional } from '@angular/core';
import { AuthObject } from './AuthObject';
import { of } from 'rxjs';
import { map } from 'rxjs/operators';
import { CheckPermissionService } from './permission/check-permission.service';
import { ActionService } from './action/action.service';
import { authActionToken, authRouteToken } from '../injectionToken';
import { IAuthAction } from './AuthAction.interface';
import { IAuthRoute } from './AuthRoute.interface';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(
    private checkPermissionService: CheckPermissionService,
    private actionService: ActionService,
    @Optional() @Inject(authActionToken) private customAuthAction: IAuthAction,
    @Optional() @Inject(authRouteToken) private customAuthRoute: IAuthRoute,
  ) { }

  async authRoute(data) {
    let result = of(new AuthObject(data)).pipe(
      map(x => this.checkPermissionService.authRoute(x))
    );
    if (this.customAuthRoute) {
      result.pipe(
        map(x => this.customAuthRoute.authRoute(x))
      )
    }
    return result.pipe(
      map(x => { return { status: x.status, error: x.error } })
    ).toPromise();
  }

  async authAction(payload) {
    let result = of(new AuthObject(payload)).pipe(
      map(x => this.actionService.authAction(x))
    );
    if (this.customAuthAction) {
      result.pipe(
        map(x => this.customAuthAction.authAction(x))
      );
    }
    return result.pipe(
      map(x => { return { status: x.status, error: x.error } })
    ).toPromise();

  }
}
