import { Pipe, PipeTransform} from '@angular/core';
import { TranslatePriceService } from './translate-price.service';


@Pipe({
  name: 'translatePrice',
  pure: false
})
export class TranslatePricePipe implements PipeTransform {

  public role: string = '';
  public million: number = 1000000;
  constructor(
    private translatePriceService: TranslatePriceService,
  ) {
  }



  transform(value: any, args?: any): string {
    return this.translatePriceService.translatePrice(value);
  }

  
}
