
import { Observable, of } from "rxjs";
import { IAPI } from "../API.interface";
import { IMockAPI } from "../MockAPI.interface";
import { ISQLiteAPI } from "../SQLiteAPI.interface";
import { ClientCustomDao, DaoFactory } from '../../device/sqlite';
import { EqualRestriction } from '../../device/sqlite';

export class DeviceAccountAPI implements IAPI, IMockAPI, ISQLiteAPI {
    private _DaoFactory: DaoFactory;
    constructor(DaoFactory: DaoFactory) {
        this._DaoFactory = DaoFactory;
    }


    getAPIName(): string {
        return 'getDeviceAccount';
    }

    getMockPath(): string {
        return './assets/mock/getDeviceAccount.json';
    }
    executeSQL(): Observable<any> {
        return Observable.create((observer) => {
            let deviceInfoObj = this._DaoFactory.getTable('Profile', "TW_LH_SD_DeviceInfo");

            let dao = this._DaoFactory.getDao('Profile');
            if (deviceInfoObj != undefined && dao != undefined) {
                dao = new ClientCustomDao(dao);
                deviceInfoObj.addRestriction(new EqualRestriction('Category', ["BindingAccount"]));
                dao.queryByTable(deviceInfoObj).subscribe((resp) => {
                    console.log("getDeviceAccount API resp:",resp);
                    observer.next(resp);
                    observer.complete();
                });
            } else {
                observer.next(false);
                observer.complete();
            }
        })
    }
}
