export class MetaGrid {
    constructor(pc: number, nb?: number, pad?: number, mobile?: number) {
        this.pc = pc;
        this.nb = nb ? nb : pc;
        this.pad = pad ? pad : pc;
        this.mobile = mobile ? mobile : pc;
    }

    public pc: number;
    public nb: number;
    public pad: number;
    public mobile: number;
}