import { Observable } from 'rxjs';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { DaoFactory } from '../../device/sqlite/DaoFactory';
import { SQLiteTable } from '../../device/sqlite/SQLiteTable';
import { ClientCustomDao } from '../../device/sqlite/ClientCustomDao';

export class SettingAPI implements IAPI, IMockAPI, ISQLiteAPI {
  private _DaoFactory: DaoFactory;
  constructor(DaoFactory) {
    this._DaoFactory = DaoFactory;
  }

  getAPIName(): string {
    return 'getSetting';
  }

  getMockPath(): string {
    return './assets/mock/getPersonalSetting.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let settingObj = this._DaoFactory.getDefaultTable("TW_LH_SD_Personal_Setting");
      let dao = this._DaoFactory.getDefaultDao();

      if (settingObj != undefined && dao != undefined) {
        settingObj = (<SQLiteTable>settingObj);
        dao = new ClientCustomDao(dao);
        dao.queryByTable(settingObj).subscribe((resp) => {
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }

}
