import { IRestriction } from '../Restriction.interface';
export class InRestriction implements IRestriction {
    private column: string;
    private values: Array<string>;
    constructor(column, values = []) {
        this.column = column;
        this.values = values;
    }
    sqlString(): string {
        var str = Array(this.values.length).fill('?').join(',');
        return `${this.column} IN (${str}) `;
    }
    getValues() {
        return this.values;
    }
}