import { IRestriction } from '../Restriction.interface';
export class LikeRestriction implements IRestriction {
    private column: string;
    private values: Array<string>;
    constructor(column, values = []) {
        this.column = column;


        let newArray = [];
        values.forEach(val => {
            newArray.push('%' + val + '%');
        });

        this.values = newArray;

        
    }
    sqlString(): string {
        return `${this.column} LIKE ? `;
    }

    getValues() {
        return this.values;
    }
}