import { Observable } from 'rxjs';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';
import { ISQLiteAPI } from '../SQLiteAPI.interface';
import { DaoFactory } from '../../device/sqlite/DaoFactory';
import { TranslateService } from '../../language/translate.service';
import { DeviceService } from '../../device/device.service';

export class SettingValueAPI implements IAPI, IMockAPI, ISQLiteAPI {
    private _translateService: TranslateService;
    private _deviceService: DeviceService
    constructor(
        translateService: TranslateService,
        deviceService: DeviceService) {
        this._translateService = translateService;
        this._deviceService = deviceService;
    }

    getAPIName(): string {
        return 'getSettingValue';
    }

    getMockPath(): string {
        return './assets/mock/getSettingValue.json';
    }

    executeSQL(): Observable<any> {
        return Observable.create((observer) => {
            let resp = {
                Language: this._translateService.getCurrentLanguage(),
                LastUpdatedTime: "Setting_See_Details"
            }
            this._deviceService.getAppVersion().then(version => {
                resp["Version"] = version;
                observer.next({ Header: { "isSuccess": true }, Body: [resp] });
                observer.complete();
            })
        });
    }

}
