export class LoginResponse {
    isSuccess: boolean;
    errorMsg: string;
    type: LOGIN_TYPE;
    token: string;
    failCount: number;
    constructor(resp: { isSuccess: boolean, type: LOGIN_TYPE, errorMsg: string, token: string, failCount?: number }) {
        this.isSuccess = resp.isSuccess;
        this.errorMsg = resp.errorMsg;
        this.type = resp.type;
        this.token = resp.token;
        this.failCount = resp.failCount || -1;
    }
}
export enum LOGIN_TYPE {
    ONLINE,
    OFFLINE
}