import { IDeviceDao } from "./DeviceDao.interface";
import { CordovaDeviceDao } from "./CordovaDeviceDao";
import { Injectable } from "@angular/core";


@Injectable({
    providedIn:'root'
})
export class DeviceFactory {
    private daoMap: Map<string, IDeviceDao> = new Map();
    private constructor() {
        this.daoMap.set("cordova", new CordovaDeviceDao());
    }
    public getDefaultDao(): IDeviceDao {
        return this.getDao("cordova");
    }

    public getDao(name: string): IDeviceDao {
        return this.daoMap.get(name);
    }



}