import { Injectable, ErrorHandler, Optional, Inject } from '@angular/core';
import { configTaskToken, registerAPITaskToken, dataSyncTaskToken, customTaskToken, initialFinishToken } from '../injectionToken';
import { IInitTask } from './interface/InitialTask.interface';
import { DataSyncTask } from './tasks/DataSyncTask';
import { ConfigTask } from './tasks/ConfigTask';
import { DeviceTask } from './tasks/DeviceTask';
import { RegisterAPITask } from './tasks/RegisterAPITask';
import { initialFinish } from './interface/initialFinish.interface';

@Injectable({
  providedIn: 'root'
})
export class InitialService {
  _sequentialTasks = [];
  _parallelTasks = [];
  constructor(
    private errorHandler: ErrorHandler,
    private dataSyncTask: DataSyncTask,
    private configTask: ConfigTask,
    private deviceTask: DeviceTask,
    private registerAPITask: RegisterAPITask,
    @Optional() @Inject(registerAPITaskToken) private customRegisterAPITask: IInitTask,
    @Optional() @Inject(customTaskToken) private customTask: IInitTask,
    @Optional() @Inject(initialFinishToken) private initialFinish: initialFinish) {
    //register sequential tasks
    if (this.registerAPITask)
      this._sequentialTasks.push(this.registerAPITask);
    if (this.customRegisterAPITask)
      this._sequentialTasks.push(this.customRegisterAPITask);
    if (this.dataSyncTask)
      this._sequentialTasks.push(this.dataSyncTask);
    if (this.deviceTask)
      this._sequentialTasks.push(this.deviceTask);
    if (this.configTask)
      this._sequentialTasks.push(this.configTask);
    if (this.customTask)
      this._sequentialTasks.push(this.customTask);

    //register parallel tasks
    // this._parallelTasks.push(this.configTask);
  }

  public async load(): Promise<any> {
    console.log('initial service load!', this._sequentialTasks);
    try {
      await this.waitnseconds(1000);
      for (let t of this._sequentialTasks) {
        await t.doTask()
      }
      await Promise.all(this._parallelTasks.map(task => task.doTask())).then(val => {
      });
      await this.dataSyncTask.refreshSchema();
      if(this.initialFinish) {
        this.initialFinish.afterInitialFinish();
      }
    } catch (err) {
      console.warn('Initial Error: maybe not on the mobile');
      this.errorHandler.handleError(err);
    }
  }


  public waitnseconds(second): Promise<any> {
    return new Promise((res, rej) => {
      setTimeout(() => {
        res();
      }, second);
    })
  }
}
