import { Injectable, Inject, Optional, ErrorHandler } from '@angular/core';
import { IFunctionSync, FUNC_STATE } from './function/IFunctionSync';
import { DaoFactory } from '../device/sqlite/DaoFactory';
import { EqualRestriction } from '../device/sqlite/restrictions/EqualRestriction';
import { ConfigToken, registerDataSyncFuncToken } from '../injectionToken/injection-token';
import { first } from 'rxjs/operators';
import { APIFactory } from '../api/APIFactory';
import { APIDispatch } from '../api/APIDispatch';
import { SyncPushAPI } from '../api/register/SyncPushAPI';
import { SyncPullAPI } from '../api/register/SyncPullAPI';
import { syncSequenceIDAPI } from '../api/register/syncSequenceIDAPI';
import { Observable, BehaviorSubject } from 'rxjs';
import { parseISO } from 'date-fns';
import { IregisterDataSyncFunc } from '../initTask/interface/registerDataSyncFunc.interface';
import * as _ from 'lodash';
import { APPError } from '../errorHandler/APPError';
import { IDao } from '../device/sqlite/Dao.interface';
import { DeviceService } from '../device/device.service';


@Injectable({
  providedIn: 'root'
})
export class DataSyncService {

  private syncAPIMap: Object = {}; // API
  private FuncAPIMap: Object = {}; // API
  private SyncProcessList: Array<string> = []; // sync
  private func_url = {};
  private syncStateSubject = new BehaviorSubject<any>(this.getCurrentSyncState());
  constructor(
    @Inject(ConfigToken) private APP_CONFIG: any,
    private APIFactory: APIFactory,
    private dispatcher: APIDispatch,
    private errorHandler: ErrorHandler,
    private DaoFactory: DaoFactory,
    private devcieService: DeviceService,
    @Inject(registerDataSyncFuncToken) private registerDataSyncFunc: IregisterDataSyncFunc
  ) {
    this.init();
  }

  private init() {
    this.func_url = this.APP_CONFIG[this.APP_CONFIG.ENV].SYNC_URL;
    this.FuncAPIMap = this.registerDataSyncFunc.getFuncMap();
    this.registerDataSyncFunc.getSyncInstance().forEach(x => {
      this.registerAPI(x);
    })
  }

  public getSyncState(): Observable<any> {
    return this.syncStateSubject.asObservable();
  }

  public registerAPI(func: IFunctionSync) {
    this.syncAPIMap[func.getName()] = func;
  }

  async syncAllFunc(async = false) {
    console.log("syncAllFunc:", this.FuncAPIMap, Object.keys(this.FuncAPIMap));
    return this.syncFunc(Object.keys(this.FuncAPIMap), async);
  }

  async syncFunc(FuncList: Array<string>, async = false) {
    console.log("SyncProcessList:", this.SyncProcessList);
    console.log("Sync Func:", FuncList);

    if (this.devcieService.getNetworkState()) {

      try {
        // If not async , filter current sync function first 
        if (!async) {
          FuncList = FuncList.filter(x => !this.SyncProcessList.includes(x));
        }

        this.SyncProcessList = [...this.SyncProcessList, ...FuncList];

        let parallelSync = [];
        let seqSync = [];

        FuncList.forEach((function_name) => {

          if (Object.keys(this.FuncAPIMap).includes(function_name)) {
            if (this.FuncAPIMap[function_name].length > 1)
              seqSync = [...seqSync, ...this.FuncAPIMap[function_name]];
            else
              parallelSync = [...parallelSync, ...this.FuncAPIMap[function_name]];
          }
        });
        console.log("SeqSync before uniq:", seqSync);
        console.log("parallelSync before uniq:", parallelSync);
        seqSync = _.uniq(seqSync);
        parallelSync = _.uniq(parallelSync);
        console.log("SeqSync after uniq:", seqSync);
        console.log("parallelSync after uniq:", parallelSync);
        console.log("SyncProcessList after uniq:", this.SyncProcessList);

        //DEV mode, skip sync
        if (this.APP_CONFIG["ENV"] == "DEV") {
          return;
        }

        let syncPromiseArray = [];
        let dao = this.DaoFactory.getDao("Profile");
        if (dao) {
          let dataSyncTimeObj = this.DaoFactory.getTable("Profile", "TW_LH_SD_DataSync_Time");
          let resp = await dao.queryByTable(dataSyncTimeObj).toPromise();
          console.log('dataSyncTime resp:', resp);
          let apiTimeMap = resp.Body;


          parallelSync.forEach((api_name) => {
            syncPromiseArray.push(this.getSyncPromiseObject(api_name, apiTimeMap, dao));
          });


          //DO PARALLEL SYNC
          await Promise.all(syncPromiseArray);

          //DO SEQ SYNC
          for (let api_name of seqSync) {
            await this.getSyncPromiseObject(api_name, apiTimeMap, dao);
          }
        }
        else
          throw new Error("Cannot Find Profile DAO.");

      } catch (error) {
        this.errorHandler.handleError(new APPError("F00110", error.message));
      }
      finally {
        this.SyncProcessList = this.SyncProcessList.filter(x => !FuncList.includes(x));
      }
    }
    else {
      console.log("It's offline now, skip syncFunc:", FuncList);
    }
  }

  private async sync(api_name: string, funcTimeMap: Array<any>) {
    let returnBackendTime: number = funcTimeMap.filter(x => (x.FuncID == api_name)).map(x => x.BackendTime)[0];
    let returnFrontendTime: number = Date.now();
    try {
      let APIInstance: IFunctionSync = this.syncAPIMap[api_name];
      let url_config = this.func_url[api_name];
      let backendTime = funcTimeMap.filter(x => (x.FuncID == api_name)).map(x => x.BackendTime)[0];
      let frontendTIme = funcTimeMap.filter(x => (x.FuncID == api_name)).map(x => x.FrontendTime)[0];

      // Debug console
      console.group(`last sync ${api_name} time:`);
      console.log("backendTime:", backendTime);
      console.log("frontendTime:", frontendTIme);
      console.groupEnd();


      // Do sync
      if (APIInstance.getState() === FUNC_STATE.AVAILABLE) {
        // Change to pending
        APIInstance.setState(FUNC_STATE.PENDING);
        this.updateState();

        let sequenceIDNumberNeed = await APIInstance.getSequentialIDNeeded(frontendTIme);
        console.log("sequenceID need:", sequenceIDNumberNeed);
        if (sequenceIDNumberNeed > 0) {
          let sequenceIDs = await this.getSequentialID(api_name, sequenceIDNumberNeed);
          await APIInstance.setSequentialID(sequenceIDs);
        }
        let pushJson = await APIInstance.getPushJson(frontendTIme);
        console.log('pushJson:', pushJson);
        if (pushJson.length > 0)
          await this.pushToServer({ data: pushJson, url: url_config.push });

        let pullJson = await this.pullFromServer({ backendTime, url: url_config.pull });
        console.log('pullJson:', pullJson);
        let pullResult = await this.syncAPIMap[api_name].pullData(pullJson);
        console.log('pull result:', pullResult);
        if (pullResult.Header.status) {
          returnBackendTime = parseISO(pullJson['lastUpdateTime']).getTime();
          returnFrontendTime = Date.now();
        }
        else {
          throw new Error(pullResult.Header.msg);
        }
      }
      else {
        await this.waitUntilSyncFinish(api_name);
      }
    } catch (error) {
      returnFrontendTime = funcTimeMap.filter(x => (x.FuncID == api_name)).map(x => x.FrontendTime)[0];
      this.errorHandler.handleError(new APPError("F00113", `${api_name} sync failed,` + error.message));
    }
    finally {
      this.syncAPIMap[api_name].setState(FUNC_STATE.AVAILABLE);
      this.updateState();
      return Promise.resolve({ backendTime: returnBackendTime, frontendTime: returnFrontendTime });
    }
  }

  private async pushToServer({ data, url }) {
    let pushAPI = this.APIFactory.getAPI("SyncPush");
    (<SyncPushAPI>pushAPI).url = url;
    (<SyncPushAPI>pushAPI).body = data;
    await this.dispatcher.dispatch(pushAPI).toPromise();
  }

  private async pullFromServer({ backendTime, url }) {
    let pullAPI = this.APIFactory.getAPI("SyncPull");
    (<SyncPullAPI>pullAPI).lastUpdateTime = new Date(backendTime).toISOString();
    (<SyncPullAPI>pullAPI).url = url;
    return await this.dispatcher.dispatch(pullAPI).toPromise()
      .then(data => {
        console.log('data from server:', data);
        return data;
      });
  }

  private getSequentialID(category: string, num: number): Promise<Array<string>> {
    let getSyncSequenceIDAPI = this.APIFactory.getAPI("getSyncSequenceID");
    if (getSyncSequenceIDAPI) {
      (<syncSequenceIDAPI>getSyncSequenceIDAPI).type = category;
      (<syncSequenceIDAPI>getSyncSequenceIDAPI).num = num;
      return this.dispatcher.dispatch(getSyncSequenceIDAPI).toPromise().then((resp) => {
        console.log("get sync sequence ID resp:", resp);
        return resp['ids'];
      })
    }

  }

  private getCurrentSyncState() {

    let state = {};
    Object.keys(this.syncAPIMap).forEach((val) => {
      state[val] = this.syncAPIMap[val].getState();
    })
    return state;
  }

  private updateState() {
    this.syncStateSubject.next(this.getCurrentSyncState());
  }


  private async waitUntilSyncFinish(func: string) {
    return this.getSyncState().pipe(
      first(x => Object.entries(x).filter(y => y[0] === func && y[1] === FUNC_STATE.AVAILABLE).length > 0)
    ).toPromise()
  }

  private async getSyncPromiseObject(api_name: string, ApiTimeMap: any, dao: IDao) {
    let { backendTime, frontendTime } = await this.sync(api_name, ApiTimeMap);
    let dataSyncTimeObj = this.DaoFactory.getTable("Profile", "TW_LH_SD_DataSync_Time");
    dataSyncTimeObj.addRestriction(new EqualRestriction('FuncID', [api_name]));
    dataSyncTimeObj.setValue('BackendTime', backendTime);
    dataSyncTimeObj.setValue('FrontendTime', frontendTime);
    let result = await dao.updateByTable(dataSyncTimeObj).toPromise();
    console.log(`Finish sync function ${api_name} :`, result);
    return result;
  }


}
