export class SQLCommand {
  private sql: string;
  private params: Array<any> = new Array<any>();


  constructor(instruction: string, params: Array<any>) {
    this.sql = instruction;
    this.params = params;
  }
  setSql(instruction: string) {
    this.sql = instruction
  }
  setParam(param: string) {
    this.params.push(param);
  }
  getSql() {
    return this.sql;
  }
  getParams() {
    return this.params;
  }
}
