import { GoalExtension } from "./GoalExtension";

export class SubmitGoalSettingValue{

    public DataType: string;
    public Value: string;

    public Extensions: Array<GoalExtension> = [];

    constructor(DataType: string, Value:string, Extensions:Array<GoalExtension>) {
        this.DataType = DataType;
        this.Value = Value;
        this.Extensions = Extensions;
    }

}