import { Injectable, ErrorHandler } from '@angular/core';

import { IAPIAccess } from '../APIAccess.interface';
import { IAPI } from '../API.interface';
import { IMockAPI } from '../MockAPI.interface';

import { HttpClient } from '@angular/common/http';
import { Observable, Observer, throwError } from 'rxjs';
import { APPError } from '../../errorHandler/APPError';
import { catchError } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class MockAPIAccess implements IAPIAccess {

    constructor(
        private httpService: HttpClient,
        private errorHandler: ErrorHandler
    ) { }

    access(api: IAPI): Observable<any> {
        return this.httpService.get((<IMockAPI>api).getMockPath()).pipe
            (catchError(error => { this.errorHandler.handleError(new APPError("F00016", error.message)); return throwError(error) })
            )
    }
}