export class AuthObject {
    public payload;
    public status: boolean;
    public error: string;
    constructor(payload: any = {}, status: boolean = true) {
        this.payload = payload;
        this.status = status;
        this.error = '';
    }
}

export enum AuthError {
    PERMISSION_DENIED = 'Permission Denied',
    TIMEOUT = 'Timeout',
    NOT_LOGIN = 'Not Login',
    PENDING_ACTION = 'Other Action Pending'
}